import React, { useEffect, useState } from 'react';
import { Button, Card, CardBody, CardFooter, CardHeader, Col, Modal, Row } from 'reactstrap';
import { Form, Formik, FormikProps } from 'formik';
import { Input } from '../common/form-helper';
import api from '../../utils/api_client';

interface FormProps {
  formikProps: FormikProps<any>;
  initClientCode: string;
  isOpen: boolean;
  clientOptions?: any;
}

const CreateClient: React.FC<FormProps> = ({ initClientCode, isOpen, clientOptions, formikProps }) => {
  const [isOpened, setIsOpened] = useState(false);

  const initValues = {
    clientCode: initClientCode,
    clientName: '',
    phoneNumber: '',
    email: '',
    address: '',
  };

  const toggleClient = () => {
    setIsOpened(!isOpened);
  };

  const handleSave = (value: {
    clientCode: string;
    clientName: string;
    phoneNumber: string;
    email: string;
    address: string;
  }) => {
    api
      .clientPost(value)
      .then(() => {
        clientOptions.push({
          value: value.clientCode,
          label: `${value.clientCode} ${value.clientName}`,
        });
        formikProps.setFieldValue('clientCode', value.clientCode);
        toggleClient();
      })
      .catch(() => toggleClient());
  };

  useEffect(() => {
    setIsOpened(isOpen);
  }, []);

  return (
    <>
      <Modal isOpen={isOpened} toggle={toggleClient} size="lg">
        <Formik initialValues={initValues} onSubmit={(value) => handleSave(value)}>
          {() => (
            <Form>
              <Card>
                <CardHeader>
                  <h3></h3>
                </CardHeader>
                <CardBody>
                  <Row className="form-group">
                    <Col className="form-group form-inline col-lg-group pr-0 pl-0">
                      <label className="col-lg-4 col-sm-5 pr-0 pl-0 required" style={{ justifyContent: 'left' }}>
                        
                      </label>
                      <div className="col-lg-8 pr-1">
                        <Input className="form-control" type="text" name="clientCode" />
                      </div>
                    </Col>
                    <Col className="form-group form-inline col-lg-group pr-0 pl-0">
                      <label className="col-lg-4 col-sm-5 pr-0 pl-0 required" style={{ justifyContent: 'left' }}>
                        
                      </label>
                      <div className="col-lg-8 pr-1">
                        <Input className="form-control" type="text" name="clientName" />
                      </div>
                    </Col>
                  </Row>
                  <Row className="form-group">
                    <Col className="form-group form-inline col-lg-group pr-0 pl-0">
                      <label className="col-lg-4 col-sm-5 pr-0 pl-0" style={{ justifyContent: 'left' }}>
                        
                      </label>
                      <div className="col-lg-8 pr-1">
                        <Input className="form-control" type="text" name="phoneNumber" />
                      </div>
                    </Col>
                    <Col className="form-group form-inline col-lg-group pr-0 pl-0">
                      <label className="col-lg-4 col-sm-5 pr-0 pl-0" style={{ justifyContent: 'left' }}>
                        
                      </label>
                      <div className="col-lg-8 pr-1">
                        <Input className="form-control" type="text" name="email" />
                      </div>
                    </Col>
                  </Row>
                  <Row className="form-group">
                    <Col className="form-group form-inline col-lg-group pr-0 pl-0">
                      <label className="col-lg-4 col-sm-5 pr-0 pl-0" style={{ justifyContent: 'left' }}>
                        
                      </label>
                      <div className="col-lg-8 pr-1">
                        <Input className="form-control" type="text" name="address" />
                      </div>
                    </Col>
                    <Col className="form-group form-inline col-lg-group pr-0 pl-0" />
                  </Row>
                </CardBody>
                <CardFooter>
                  <div className="pull-right">
                    <Button className="mr-3" variant="info" type="submit">
                      
                    </Button>
                    <Button color="light" onClick={toggleClient}>
                      
                    </Button>
                  </div>
                </CardFooter>
              </Card>
            </Form>
          )}
        </Formik>
      </Modal>
    </>
  );
};
export default CreateClient;
