import React, { useCallback } from 'react';
import ReactDataTable, { IDataTableProps, defaultThemes, IDataTableColumn } from 'react-data-table-component';
import Spinner from './spinner';

import CustomPagination, { CustomPaginationProps } from './custom-pagination';

export type DataTableColumn = IDataTableColumn;

const centerHeader = {
  margin: '',
  '> div': {
    margin: '',
  },
};

const customStyles = {
  header: {
    style: {
      minHeight: '56px',
    },
  },
  headRow: {
    style: {
      borderTopStyle: 'solid',
      borderTopWidth: '1px',
      borderTopColor: defaultThemes.default.divider.default,
    },
  },
  headCells: {
    style: {
      borderRightStyle: 'solid',
      borderRightWidth: '1px',
      borderRightColor: defaultThemes.default.divider.default,
      ':first-child': {
        borderLeftStyle: 'solid',
        borderLeftWidth: '1px',
        borderLeftColor: defaultThemes.default.divider.default,
      },
      ...centerHeader,
    },
  },
  cells: {
    style: {
      borderRightStyle: 'solid',
      borderRightWidth: '1px',
      borderRightColor: defaultThemes.default.divider.default,
      ':first-child': {
        borderLeftStyle: 'solid',
        borderLeftWidth: '1px',
        borderLeftColor: defaultThemes.default.divider.default,
      },
    },
  },
};

interface DataTableProps extends IDataTableProps {
  loading?: boolean;
  border?: boolean;
  textPaging?: (rowCount: number, currentRowRange: string) => React.ReactElement;
}

const DataTable: React.FC<DataTableProps> = (props: DataTableProps) => {
  const noRecordMessage = <h5></h5>;

  const {
    striped = true,
    pagination = true,
    loading = true,
    border = false,
    className = 'dataTable',
    noDataComponent = noRecordMessage,
    paginationPerPage,
    data,
  } = props;

  const textPaging = (rowCount: number, currentRowRange: string) => {
    return (
      <>
        {rowCount} {currentRowRange}
      </>
    );
  };

  const clonePagination = useCallback(
    (paginationProps: CustomPaginationProps) => {
      const { currentPage, onChangePage, onChangeRowsPerPage, rowCount, rowsPerPage } = paginationProps;
      return (
        <CustomPagination
          paginationPerPage={paginationPerPage}
          currentPage={currentPage}
          onChangePage={onChangePage}
          onChangeRowsPerPage={onChangeRowsPerPage}
          rowCount={rowCount}
          rowsPerPage={rowsPerPage}
          textPaging={textPaging}
          border={border}
        />
      );
    },
    [data]
  );

  return (
    <Spinner loading={loading}>
      <ReactDataTable
        {...props} // eslint-disable-line react/jsx-props-no-spreading
        striped={striped}
        pagination={pagination}
        className={className}
        noDataComponent={noDataComponent}
        paginationComponent={clonePagination}
        customStyles={
          border
            ? customStyles
            : {
                headCells: {
                  style: centerHeader,
                },
              }
        }
      />
    </Spinner>
  );
};

export default DataTable;
