import { Button, Card, CardBody, CardFooter, CardHeader, Col, Modal, Row } from 'reactstrap';
import React, { useEffect, useState } from 'react';
import { Form, Formik, FormikProps } from 'formik';
import { CustomSelect, Input } from '../common/form-helper';
import { EmployeeAttributes, RoleAttributes } from '../../api';
import api from '../../utils/api_client';

interface FormProps {
  parentFormikProps: FormikProps<any>;
  currentIndex: number;
  dataProps: string | undefined;
  isOpen: boolean;
  memberOptions: { value: string; label: string }[];
}
interface CreateProps extends EmployeeAttributes {
  roleId: number;
  roleName: string;
}

const CreateEmployee: React.FC<FormProps> = ({ dataProps, isOpen, memberOptions, parentFormikProps, currentIndex }) => {
  const [isOpened, setIsOpened] = useState(false);
  const [createRole, setCreateRole] = useState(false);
  const [roleOptions, setRoleOptions] = useState<{ value: number; label: string }[]>([]);

  const initData: CreateProps = {
    employeeCode: dataProps || '',
    employeeName: '',
    departmentName: '',
    email: '',
    postalCode: '',
    phoneNumber: '',
    roleId: 1,
    roleName: '',
  };

  useEffect(() => {
    api.roleGetAll().then((res) => {
      const roleList = res.data.map((role) => ({
        value: role.roleId,
        label: role.roleName,
      }));
      setRoleOptions(roleList);
    });
    setIsOpened(isOpen);
  }, []);

  const toggleConfirm = () => {
    setIsOpened(!isOpened);
  };

  const toggleRole = () => {
    setCreateRole(!createRole);
  };

  const handleSave = (value: CreateProps) => {
    if (createRole) {
      const createRoleValue: RoleAttributes = { roleId: value.roleId, roleName: value.roleName };
      api
        .rolePost(createRoleValue)
        .then(() => {
          roleOptions.push({
            value: value.roleId,
            label: value.roleName,
          });
          toggleRole();
        })
        .catch(() => toggleRole());
    } else {
      const createEmployeeValue = {
        employeeCode: value.employeeCode,
        employeeName: value.employeeName,
        departmentName: value.departmentName,
        email: value.email,
        postalCode: value.postalCode,
        phoneNumber: value.phoneNumber,
        roleId: value.roleId,
      };
      api
        .createEmployee(createEmployeeValue)
        .then(() => {
          memberOptions.push({
            value: value.employeeCode,
            label: value.employeeName,
          });
          parentFormikProps.setFieldValue(`details[${currentIndex}].memberCode`, memberOptions.slice(-1)[0].value);
          parentFormikProps.setFieldValue(`details[${currentIndex}].memberName`, memberOptions.slice(-1)[0].label);
          toggleConfirm();
        })
        .catch(() => toggleConfirm());
    }
  };
  return (
    <>
      <Modal isOpen={isOpened} toggle={toggleConfirm} size="lg">
        <Formik initialValues={initData} onSubmit={(value) => handleSave(value)}>
          {(formikProps) => (
            <Form>
              {createRole ? (
                <Card>
                  <CardHeader>
                    <h3></h3>
                  </CardHeader>
                  <CardBody>
                    <Row className="form-group">
                      <Col className="form-group form-inline col-lg-group pr-0 pl-0">
                        <label className="col-lg-4 col-sm-5 pr-0 pl-0 required" style={{ justifyContent: 'left' }}>
                          ID
                        </label>
                        <div className="col-lg-8 pr-1">
                          <Input className="form-control" type="text" name="roleId" />
                        </div>
                      </Col>
                      <Col className="form-group form-inline col-lg-group pr-0 pl-0">
                        <label className="col-lg-4 col-sm-5 pr-0 pl-0 required" style={{ justifyContent: 'left' }}>
                          
                        </label>
                        <div className="col-lg-8 pr-1">
                          <Input className="form-control" type="text" name="roleName" />
                        </div>
                      </Col>
                    </Row>
                  </CardBody>
                  <CardFooter>
                    <div className="pull-right">
                      <Button
                        className="mr-3"
                        variant="info"
                        type="submit"
                        onClick={() =>
                          formikProps.setFieldValue(
                            'roleId',
                            roleOptions.slice(-1)[0]?.value ? Number(roleOptions.slice(-1)[0].value) + 1 : 1
                          )
                        }
                      >
                        
                      </Button>
                      <Button color="light" onClick={toggleRole}>
                        
                      </Button>
                    </div>
                  </CardFooter>
                </Card>
              ) : (
                <Card>
                  <CardHeader>
                    <h3></h3>
                  </CardHeader>
                  <CardBody>
                    <>
                      <div className="form-group">
                        <Row>
                          <Col className="form-group form-inline col-lg-group pr-0 pl-0" />
                          <Col className="form-group form-inline col-lg-group pr-1 pl-0">
                            <label className="col-lg-4 col-sm-5 pr-0 pl-0" style={{ justifyContent: 'left' }}>
                              
                            </label>
                            <div className="col-lg-8 pr-3">
                              <Row>
                                <div style={{ width: '90%' }} className="pl-3 pr-1">
                                  <CustomSelect name="roleId" options={roleOptions} />
                                </div>
                                <i
                                  style={{ width: '10%' }}
                                  className="pointer icofont icofont-ui-add pl-1 mt-2 pt-1"
                                  onClick={() => {
                                    formikProps.setFieldValue(
                                      'roleId',
                                      roleOptions.slice(-1)[0]?.value
                                        ? String(Number(roleOptions.slice(-1)[0].value) + 1)
                                        : '1'
                                    );
                                    toggleRole();
                                  }}
                                />
                              </Row>
                            </div>
                          </Col>
                        </Row>
                        <Row>
                          <Col className="form-group form-inline col-lg-group pr-0 pl-0">
                            <label className="col-lg-4 col-sm-5 pr-0 pl-0 required" style={{ justifyContent: 'left' }}>
                              
                            </label>
                            <div className="col-lg-8 pr-4">
                              <Input style={{ width: '95%' }} type="text" name="employeeCode" />
                            </div>
                          </Col>
                          <Col className="form-group form-inline col-lg-group pr-0 pl-0">
                            <label className="col-lg-4 col-sm-5 pr-0 pl-0 required" style={{ justifyContent: 'left' }}>
                              
                            </label>
                            <div className="col-lg-8 pr-4">
                              <Input style={{ width: '95%' }} type="text" name="employeeName" />
                            </div>
                          </Col>
                        </Row>
                        <Row>
                          <Col className="form-group form-inline col-lg-group pr-0 pl-0">
                            <label className="col-lg-4 col-sm-5 pr-0 pl-0" style={{ justifyContent: 'left' }}>
                              
                            </label>
                            <div className="col-lg-8 pr-4">
                              <Input style={{ width: '95%' }} type="text" name="departmentName" />
                            </div>
                          </Col>
                          <Col className="form-group form-inline col-lg-group pr-0 pl-0">
                            <label className="col-lg-4 col-sm-5 pr-0 pl-0" style={{ justifyContent: 'left' }}>
                              
                            </label>
                            <div className="col-lg-8 pr-4">
                              <Input style={{ width: '95%' }} type="text" name="email" />
                            </div>
                          </Col>
                        </Row>
                        <Row>
                          <Col className="form-group form-inline col-lg-group pr-0 pl-0">
                            <label className="col-lg-4 col-sm-5 pr-0 pl-0" style={{ justifyContent: 'left' }}>
                              
                            </label>
                            <div className="col-lg-8 pr-4">
                              <Input style={{ width: '95%' }} type="text" name="postalCode" />
                            </div>
                          </Col>
                          <Col className="form-group form-inline col-lg-group pr-0 pl-0">
                            <label className="col-lg-4 col-sm-5 pr-0 pl-0" style={{ justifyContent: 'left' }}>
                              
                            </label>
                            <div className="col-lg-8 pr-4">
                              <Input style={{ width: '95%' }} type="text" name="phoneNumber" />
                            </div>
                          </Col>
                        </Row>
                      </div>
                    </>
                  </CardBody>
                  <CardFooter>
                    <Row>
                      <div className="col align-self-start" />
                      <div>
                        <Button variant="info" type="submit">
                          
                        </Button>
                      </div>
                      <div className="col align-self-end" />
                    </Row>
                  </CardFooter>
                </Card>
              )}
            </Form>
          )}
        </Formik>
      </Modal>
    </>
  );
};
export default CreateEmployee;
