import React, { useMemo, useCallback, useEffect, useState } from 'react';
import Select, { OptionsType } from 'react-select';
import { defaultThemes } from 'react-data-table-component';
import { ButtonGroup, Button } from 'reactstrap';

type OptionType = {
  label: string;
  value: string | number;
  isDisabled?: boolean;
};

export interface CustomPaginationProps {
  rowsPerPage: number;
  rowCount: number;
  paginationPerPage?: number;
  onChangePage: (page: number, totalRows: number) => void;
  onChangeRowsPerPage: (currentRowsPerPage: number, currentPage: number) => void;
  currentPage: number;
  textPaging?: (rowCount: number, currentRowRange: string) => React.ReactElement;
  border?: boolean;
}

const defaultRowPerPage = [10, 15, 20, 25, 30];

const getNumberOfPages = (rowCount: number, rowsPerPage: number) => Math.ceil(rowCount / rowsPerPage);

const getRangeRow = (rowCount: number, totalPage: number, currentPage: number, rowsPerPage: number) => {
  const currentRow = rowsPerPage * currentPage - rowsPerPage;
  return `${currentRow + 1}-${
    currentPage < totalPage ? currentRow + rowsPerPage : currentRow + (rowCount - currentRow)
  }`;
};

const generateRowOptions = (options: number[]): OptionsType<{ value: number; label: number }> => {
  return options.map((option) => ({ value: option, label: option }));
};

const CustomPagination: React.FC<CustomPaginationProps> = ({
  rowsPerPage,
  rowCount,
  onChangePage,
  onChangeRowsPerPage,
  currentPage,
  paginationPerPage = defaultRowPerPage[0],
  textPaging = (total: number, currentRowRange: string) => (
    <>
      {total}{currentRowRange}
    </>
  ),
  border,
}) => {
  const [pageOptions, setPageOptions] = useState<OptionsType<{ value: number; label: number }>>([]);
  const [defaultOptions] = useState<{ value: number; label: number }>({
    value: paginationPerPage,
    label: paginationPerPage,
  });

  useEffect(() => {
    const pageSet = new Set(defaultRowPerPage);
    pageSet.add(paginationPerPage);
    const pages = Array.from(pageSet).sort((a, b) => {
      return a - b;
    });
    setPageOptions(generateRowOptions(pages));
    onChangeRowsPerPage(paginationPerPage, currentPage);
  }, [paginationPerPage]);

  const numberOfPage = useMemo(() => {
    return getNumberOfPages(rowCount, rowsPerPage);
  }, [rowCount, rowsPerPage]);

  const currentRowRange = getRangeRow(rowCount, numberOfPage, currentPage, rowsPerPage);

  const handleFirstPageButtonClick = useCallback(() => {
    onChangePage(1, rowCount);
  }, [onChangePage, rowCount]);

  const handleBackButtonClick = useCallback(() => {
    onChangePage(currentPage - 1, rowCount);
  }, [onChangePage, currentPage, rowCount]);

  const handleNextButtonClick = useCallback(() => {
    onChangePage(currentPage + 1, rowCount);
  }, [onChangePage, currentPage, rowCount]);

  const handleLastPageButtonClick = useCallback(() => {
    onChangePage(numberOfPage, rowCount);
  }, [onChangePage, numberOfPage, rowCount]);

  const Actions = (
    <div style={{ lineHeight: '50px' }}>
      <ButtonGroup>
        <Button color="none" style={{borderColor:"hsl(0,0%,80%)", color:"black"}} disabled={currentPage === 1} onClick={handleFirstPageButtonClick}>
          <i className="fa fa-angle-double-left"/>
        </Button>
        <Button color="none" style={{borderColor:"hsl(0,0%,80%)", color:"black"}} disabled={currentPage === 1} onClick={handleBackButtonClick}>
          <i className="fa fa-angle-left"/>
        </Button>
        <Button color="none" style={{borderColor:"hsl(0,0%,80%)", color:"black"}} disabled={currentPage === numberOfPage} onClick={handleNextButtonClick}>
          <i className="fa fa-angle-right"/>
        </Button>
        <Button color="none" style={{borderColor:"hsl(0,0%,80%)", color:"black"}} disabled={currentPage === numberOfPage} onClick={handleLastPageButtonClick}>
          <i className="fa fa-angle-double-right"/>
        </Button>
      </ButtonGroup>
    </div>
  );

  const borderStyle: React.CSSProperties = {
    border: `1px solid ${defaultThemes.default.divider.default}`,
  };

  const selectStyles = {
    container: (base: React.CSSProperties) => ({
      ...base,
      width: 80,
    }),
  };

  const paginationWidth = (document.getElementById('datatable-pagination')?.offsetWidth || 536) + 14;

  return (
    <div
      id="datatable-pagination"
      className="d-inline-flex pl-3 pr-3"
      style={{
        height: 56,
        position: 'sticky',
        left: `calc(100% - ${paginationWidth}px)`,
        ...(border ? borderStyle : {}),
      }}
    >
      <div className="mr-5" style={{ display: 'flex', alignItems: 'center' }}>
        <span className="mr-2"></span>
        <Select
          options={pageOptions}
          menuPosition="fixed"
          menuShouldBlockScroll
          styles={selectStyles}
          defaultValue={defaultOptions}
          onChange={(value: unknown) => onChangeRowsPerPage(Number(((value as unknown) as OptionType)?.value), currentPage)}
          isSearchable={false}
        />
      </div>
      <div className="mr-5" style={{ lineHeight: '56px' }}>
        {textPaging(rowCount, currentRowRange)}
      </div>
      {Actions}
    </div>
  );
};

export default CustomPagination;
