import axios from 'axios';

/**
 * axios
 * axiosimport
 */
axios.defaults.headers.common['X-Requested-With'] = 'XMLHttpRequest';
axios.defaults.withCredentials = true;

// API
if (process.env.NODE_ENV !== 'production') {
  axios.defaults.baseURL = 'http://localhost:3000/api';
}

axios.interceptors.response.use(
  (response) => response,
  async (error) => {
    if (error.response) {
      if (error.response.status === 401) {
        // 
        const LOGIN_URL = process.env.NODE_ENV === 'production' ? '/login' : 'http://localhost:3000/login';
        window.location.replace(LOGIN_URL);
      } else if (error.response.status >= 500) {
        if (process.env.NODE_ENV === 'production') {
          localStorage.setItem('jhc.error', JSON.stringify(error.response.data));
          window.location.href = '/ui/system_error';
        } else {
          // eslint-disable-next-line no-alert
          window.alert(JSON.stringify(error.response.data, null, 2));
        }
      } else {
        const title = `: ${error.response.data.code}`;
        window.alert(JSON.stringify(title, null, 2));
      }
    }
    return Promise.reject(error);
  }
);

export default axios;
