import React, { PropsWithChildren, ReactNode } from 'react';
import Select, { OptionsType } from 'react-select';
import CreatableSelect from 'react-select/creatable';
import { useField, Field, FieldAttributes } from 'formik';
import { MenuPosition } from 'react-select/src/types';

interface FieldProps extends FieldAttributes<any> {
  className?: string;
  onValueChange?: (value: string | number) => void;
  addonBefore?: string | React.ReactNode;
  addonAfter?: string | React.ReactNode;
  onClick?: () => void;
}

const BootstrapField = (as: string, props: PropsWithChildren<FieldProps>) => {
  const { className, onValueChange, children, name, addonBefore, addonAfter, ...other } = props;
  const [field, meta] = useField(name);
  const classNames = ['form-control'];
  if (meta.touched && meta.error) {
    classNames.push('is-invalid');
  }
  if (className) {
    classNames.push(className);
  }
  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    field.onChange(e);
    if (onValueChange) {
      onValueChange(e.currentTarget.value);
    }
  };

  const AddonBefore = (
    <span className="input-group-prepend">
      <span className="input-group-text">{addonBefore}</span>
    </span>
  );

  const AddonAfter = (
    <span className="input-group-append">
      <span className="input-group-text">{addonAfter}</span>
    </span>
  );

  const renderField = (
    <Field
      as={as}
      className={classNames.join(' ')}
      {...field} // eslint-disable-line react/jsx-props-no-spreading
      {...other} // eslint-disable-line react/jsx-props-no-spreading
      onChange={(e: React.ChangeEvent<HTMLInputElement>) => handleChange(e)}
    >
      {children}
    </Field>
  );

  return (
    <>
      {addonBefore || addonAfter ? (
        <>
          <div className="input-group">
            {addonBefore && AddonBefore}
            {renderField}
            {addonAfter && AddonAfter}
            {meta.touched && meta.error && <div className="invalid-feedback">{meta.error}</div>}
          </div>
        </>
      ) : (
        <>
          {renderField}
          {meta.touched && meta.error && <div className="invalid-feedback">{meta.error}</div>}
        </>
      )}
    </>
  );
};

/**
 * input+
 * ) <Input className='col-6' name='productName' onValueChange={(productName) => console.log(productName)} />
 */
export const Input: React.FC<FieldProps> = (props: FieldProps) => {
  return BootstrapField('input', props);
};

/**
 * textarea+
 * ) <Textarea
 *      className='col-6'
 *      name='description'
 *      onValueChange={(description) => console.log(description)} rows={5} />
 */
export const Textarea: React.FC<FieldProps> = (props: FieldProps) => {
  return BootstrapField('textarea', props);
};

/**
 * select+
 * ) <DefaultSelect name='eastWestKbn'>
 *       <option/>
 *       <option value='1'></option>
 *       <option value='2'></option>
 *     </DefaultSelect>
 */
export const DefaultSelect: React.FC<FieldProps> = (props: PropsWithChildren<FieldProps>) => {
  return BootstrapField('select', props);
};

export type OptionType = {
  label: string;
  value: string | number;
  isDisabled?: boolean;
};

interface ReactSelectProps {
  /**
   * name
   */
  name: string;

  /**
   * labelvalue
   */
  options: OptionsType<OptionType>;

  /**
   * Blue
   */
  onBlur?: () => void;

  /**
   * true
   */
  isClearable?: boolean;

  /**
   * 
   */
  placeholder?: string;
}

interface CustomSelectProps extends ReactSelectProps {
  /**
   * Change
   * @param value 
   */
  onChange?: (value: string | string[] | number | number[] | null) => void;
  onInputChange?: (value: string | number | null) => void | undefined;
  onMenuOpen?: () => void;
  /**
   * true
   */
  isMulti?: boolean;

  /**
   * option
   * 
   */
  noOptionsMessage?: string;

  isDisabled?: boolean;
  menuPosition?: MenuPosition;
}

/**
 * react-select
 * 
 * ) const warehouseOptions = [
 *       { value: '860', label: '' },
 *       { value: '974', label: '' },
 *       ...
 *     ];
 * ...
 * <CustomSelect name='warehouseCode' options={warehouseOptions} />
 *
 * 
 * https://gist.github.com/hubgit/e394e9be07d95cd5e774989178139ae8
 */
export const CustomSelect: React.FC<CustomSelectProps> = (props: CustomSelectProps) => {
  const { name, options, isClearable, isMulti, placeholder, isDisabled, menuPosition } = props;
  const customStyle = {
    menu: (provided: any) => ({
      ...provided,
      zIndex: 2,
    }),
  };
  const [field, meta, helper] = useField(name);
  const classNames = [];
  if (meta.touched && meta.error) {
    classNames.push('is-invalid');
  }
  const handleChange = (option: {
    map: (arg0: (item: OptionType) => string | number) => string | number | string[] | number[] | null;
    value: string | number | string[] | number[] | null;
  }) => {
    let value: string | string[] | number | number[] | null;
    if (option) {
      value = props.isMulti ? option.map((item: OptionType) => item.value) : option.value;
    } else {
      value = props.isMulti ? [] : null;
    }
    helper.setTouched(true);
    helper.setValue(value);
    if (props.onChange) {
      props.onChange(value);
    }
  };
  const handleBlur = () => {
    helper.setTouched(true);
    if (props.onBlur) {
      props.onBlur();
    }
  };
  const getValue = () => {
    if (props.options) {
      const value = props.isMulti
        ? props.options.filter((option) => field.value.indexOf(option.value) >= 0)
        : props.options.find((option) => option.value === field.value);

      return !props.isMulti && field.value === '' ? '' : value;
    }
    return props.isMulti ? [] : null;
  };
  const handleInputchange = (e: string | number | null) => {
    if (props.onInputChange) {
      props.onInputChange(e);
    }
  };
  const onMenuOpen = () => {
    if (props.onMenuOpen) {
      props.onMenuOpen();
    }
  };

  return (
    <>
      <Select
        styles={customStyle}
        name={field.name}
        value={getValue() as []}
        // @ts-ignore
        onChange={handleChange}
        onBlur={handleBlur}
        onInputChange={(e) => handleInputchange(e)}
        onMenuOpen={() => onMenuOpen()}
        options={options}
        isClearable={isClearable}
        isMulti={isMulti}
        placeholder={placeholder || ''}
        isDisabled={isDisabled}
        noOptionsMessage={() => props.noOptionsMessage || ''}
        className={classNames.join(' ')}
        menuPosition={menuPosition}
      />
      {meta.touched && meta.error && <div className="invalid-feedback">{meta.error}</div>}
    </>
  );
};

interface StaticCreatableSelectProps extends ReactSelectProps {
  /**
   * Change
   * @param value 
   */
  onChange?: (value: string | string[] | number | number[] | null) => void;

  /**
   * 
   * ${str}
   * @param str 
   */
  formatCreateLabel?: (str: string) => ReactNode;
}

/**
 * react-select
 * 
 */
export const StaticCreatableSelect: React.FC<StaticCreatableSelectProps> = (props: StaticCreatableSelectProps) => {
  const { name, options, isClearable, placeholder, formatCreateLabel } = props;
  const [field, meta, helper] = useField(name);
  const classNames = [];
  if (meta.touched && meta.error) {
    classNames.push('is-invalid');
  }

  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  const handleChange = (option: any) => {
    let value: string | number | null;
    if (option) {
      value = option.value;
    } else {
      value = null;
    }
    helper.setValue(value);
    helper.setTouched(true);
    if (props.onChange) {
      props.onChange(value);
    }
  };
  const handleBlur = () => {
    helper.setTouched(true);
    if (props.onBlur) {
      props.onBlur();
    }
  };
  const getValue = () => {
    if (options) {
      return props.options.find((option) => option.value === field.value);
    }
    return null;
  };

  return (
    <>
      <CreatableSelect
        name={field.name}
        value={getValue() as OptionType}
        onChange={handleChange}
        onBlur={handleBlur}
        options={options}
        isClearable={isClearable}
        placeholder={placeholder || ''}
        formatCreateLabel={formatCreateLabel || ((str: string) => `${str}`)}
        className={classNames.join(' ')}
      />
      {meta.touched && meta.error && <div className="invalid-feedback">{meta.error}</div>}
    </>
  );
};

// eslint-disable-next-line @typescript-eslint/no-explicit-any
interface CheckboxProps extends FieldAttributes<any> {
  name: string;
  label?: string;
  id?: string; // nameid
  onValueChange?: (checked: boolean) => void;
}

/**
 * idnameid
 * 
 * ) <Checkbox name='inventoryManagementFlg' label='' />
 */
export const Checkbox: React.FC<CheckboxProps> = (props: CheckboxProps) => {
  const { name, label, id, onValueChange } = props;
  const [field] = useField({ name, type: 'checkbox' });
  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    field.onChange(e);
    if (onValueChange) {
      onValueChange(e.currentTarget.checked);
    }
  };
  return (
    <div className="form-check">
      <input
        className="form-check-input"
        type="checkbox"
        id={id || name}
        checked={field.checked}
        onChange={(e: React.ChangeEvent<HTMLInputElement>) => handleChange(e)}
      />
      <label className="form-check-label ml-1" htmlFor={id || name}>
        {label}
      </label>
    </div>
  );
};

/**
 * idnameid
 * 
 * ) <Switch name='inventoryManagementFlg' label='' />
 */
export const Switch: React.FC<CheckboxProps> = (props: CheckboxProps) => {
  const { name, label, id, onValueChange } = props;
  const [field] = useField({ name, type: 'checkbox' });
  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    field.onChange(e);
    if (onValueChange) {
      onValueChange(e.currentTarget.checked);
    }
  };
  return (
    <div className="custom-control custom-switch">
      <input
        type="checkbox"
        className="custom-control-input"
        id={id || name}
        onChange={(e: React.ChangeEvent<HTMLInputElement>) => handleChange(e)}
      />
      <label className="custom-control-label" htmlFor={id || name}>
        {label}
      </label>
    </div>
  );
};
