import React from 'react';
import { formatPrice } from './formatter/formatter';
import { REMARKS_MAP } from '../../constant/commonConstant';

export interface OptionsType {
  value: string;
  label: string;
}

// eslint-disable-next-line @typescript-eslint/explicit-module-boundary-types
export const columns = (membersOptions: OptionsType[]) => [
  {
    name: '',
    selector: 'note',
    sortable: true,
    minWidth: '20%',
    cell: (row: { note: string; remarks: string; person: string }) => (
      <span>
        {row.remarks === '4' ? membersOptions.filter((item) => item.value === row.person)[0]?.label : row.note}
      </span>
    ),
  },
  {
    name: '',
    selector: 'quantity',
    sortable: true,
    right: true,
    minWidth: '10%',
    cell: (row: { quantity: number }) => <span>{row.quantity ? row.quantity : ''}</span>,
  },
  {
    name: '',
    selector: 'unit',
    sortable: true,
    minWidth: '10%',
    cell: (row: { unit: string }) => <span>{row.unit}</span>,
  },
  {
    name: '',
    selector: 'unitPrice',
    sortable: true,
    minWidth: '10%',
    cell: (row: { unitPrice: string }) => <span>{row.unitPrice}</span>,
  },
  {
    name: '',
    selector: 'price',
    sortable: true,
    right: true,
    minWidth: '15%',
    cell: (row: { price: number; quantity: number; unitPrice: number; remarks: string }) => (
      <span style={{ color: row.remarks === '1' ? 'red' : undefined }}>
        {row.remarks === '1' && ' '}
        {/* eslint-disable-next-line no-nested-ternary */}
        {row.quantity && row.unitPrice ? formatPrice(row.quantity * row.unitPrice) : ''}
      </span>
    ),
  },
  {
    name: '',
    selector: 'remarks',
    sortable: true,
    minWidth: '20%',
    cell: (row: { remarks: string }) => <span>{REMARKS_MAP[row.remarks]}</span>,
  },
  {
    name: '',
    minWidth: '4%',
  },
];
