import React from 'react';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import { Provider } from 'react-redux';
import { initYupLocale } from './constant/locale-ja'; // Don't move this
import { Home } from './pages/home';
import { ScreenUrl } from './constant/commonConstant';
import OrderManagement from './pages/s0010';
import Service from './pages/s0011';
import Estimate from './pages/s0012';
import Order from './pages/s0013';
import Invoice from './pages/s0014';
import WorkReport from './pages/s0015';
import store from './store/index';
import ScrollToTop from './components/common/scroll-top-route';
import './App.css';

initYupLocale();

function App(): JSX.Element {
  return (
    <Provider store={store}>
      <Router>
        <div>
          <ScrollToTop />
          <Switch>
            <Route exact path="/" component={Home} />
            <Route exact path={ScreenUrl.ORDER_MANAGEMENT} component={OrderManagement} />
            <Route exact path={ScreenUrl.SERVICE_CREATE} component={Service} />
            <Route exact path={`${ScreenUrl.SERVICE_UPDATE}:id`} component={Service} />
            <Route exact path={ScreenUrl.ESTIMATE_CREATE} component={Estimate} />
            <Route exact path={`${ScreenUrl.ESTIMATE_UPDATE}:id`} component={Estimate} />
            <Route exact path={ScreenUrl.ORDER_CREATE} component={Order} />
            <Route exact path={`${ScreenUrl.ORDER_UPDATE}:id`} component={Order} />
            <Route exact path={ScreenUrl.INVOICE_CREATE} component={Invoice} />
            <Route exact path={`${ScreenUrl.INVOICE_UPDATE}:id`} component={Invoice} />
            <Route exact path={`${ScreenUrl.WORK_REPORT}:id`} component={WorkReport} />
          </Switch>
        </div>
      </Router>
    </Provider>
  );
}

export default App;
