import React, { useState, useEffect } from 'react';
import { Row, Col, Card, CardBody, CardHeader, Button, CardFooter, Alert } from 'reactstrap';
import { useHistory, useLocation, useParams } from 'react-router';
import { FieldArray, Form, Formik, FormikHelpers, FormikProps } from 'formik';
import { Link } from 'react-router-dom';
import * as Yup from 'yup';
import Breadcrumb from '../components/common/breadcrumb';
import AppLayout from '../components/common/layout';
import DataTable from '../components/common/datatable/data-table';
import { formatDate, formatPrice, padId } from '../components/common/formatter/formatter';
import { CustomSelect, Input } from '../components/common/form-helper';
import DocumentUploader from '../components/common/document-uploader';
import { ScreenUrl, remarksOptions, STATUS_MAP, Status, Remark, ZERO } from '../constant/commonConstant';
import CustomSidebar from '../components/common/custom-sidebar';
import api from '../utils/api_client';
import CreateEmployee from '../components/employee-process/create-employee';
import {
  DocumentAttributes,
  EstimateCreateAttributes,
  InvoiceDetailAttributes,
  OrderCreateAttributes,
  InvoiceCreateAttributes,
  InvoiceUpdateAttributes,
  InvoiceDocumentAttributes,
} from '../api';
import { columns, OptionsType } from '../components/common/detail-form-common';
import ConfirmModal from '../components/common/confirm-modal';

export interface Detail {
  createdAt?: string;
  invoiceCode?: string;
  orderDetailId?: number;
  invoiceDetailId?: number;
  updatedAt?: string;
  person?: string;
  note: string;
  quantity?: number;
  unit?: string;
  unitPrice?: number;
  price?: number;
  remarks: string;
  newFlg?: boolean;
}

export interface InitialValue {
  projectCode?: string;
  projectName?: string;
  serviceId: number;
  serviceCode?: string;
  serviceName?: string;
  clientName?: string;
  status: string;
  details: Detail[];
  invoiceDate: string;
  orderId: number;
  orderNumber?: string;
  orderName?: string;
  memo?: string;
  invoiceId: number;
  invoiceCode: string;
  documents?: invoiceDocument[];
  clientCode?: string;
  serviceStartDate?: string;
  serviceEndDate?: string;
  paymentDate: string;
}

const blankDocumentInfo: invoiceDocument = {
  documentId: 0,
  documentName: '',
  filePath: '',
  documentSaveName: '',
};

interface invoiceDocument extends DocumentAttributes {
  filesInfo?: File;
}

const tableData: Detail[] = [];

const blankDetail: Detail = {
  person: '',
  note: '',
  quantity: 0,
  unit: '',
  unitPrice: 0,
  price: 0,
  remarks: '',
  newFlg: false,
};

const textDetail: Detail = {
  person: '',
  note: '',
  quantity: 0,
  unit: '',
  unitPrice: 0,
  price: 0,
  remarks: Remark.TEXT,
};

const initialDnDState = {
  draggedFrom: -1,
  draggedTo: -1,
  isDragging: false,
  originalOrder: tableData,
  updatedOrder: tableData,
};

interface linkScreen {
  name: string;
  link: string;
  data: { id: number; code: string; status: string };
}

const SchemaValidation = Yup.object().shape({
  invoiceCode: Yup.string().nullable().required().length(12),
  paymentDate: Yup.string().nullable().required(),
  invoiceDate: Yup.string().nullable().required(),
});

const draftSchemaValidation = Yup.object().shape({
  invoiceCode: Yup.string().nullable().required().length(12),
});

const Invoice: React.FC = () => {
  // eslint-disable-next-line @typescript-eslint/ban-ts-comment
  // @ts-ignore
  const { id } = useParams();
  const history = useHistory();
  const location = useLocation();
  const [loading, setLoading] = useState(false);
  const [status, setStatus] = useState('');
  const [schema, setSchema] = useState(draftSchemaValidation);
  const [modalMember, setModalMember] = useState(false);
  const [deleteConfirm, setDeleteConfirm] = useState(false);
  const [createConfirm, setCreateConfirm] = useState(false);
  const [submit, setSubmit] = useState<boolean | undefined>(undefined);
  const [currentIndex, setCurrentIndex] = useState(0);
  const [editFlag, setEditFlag] = useState(false);
  const [noScroll, setNoScroll] = useState(false);
  const [savedAlert, setSavedAlert] = useState<{ value: boolean; message: string }>({ value: false, message: '' });
  const [savedTempAlert, setSavedTempAlert] = useState<{ value: boolean; message: string }>({
    value: false,
    message: '',
  });
  const [membersOptions, setMembersOptions] = useState<OptionsType[]>([]);
  const [data, setData] = useState<InitialValue>();
  const [linkData, setLinkData] = useState<linkScreen[]>([]);
  const [initFindData, setInitFindData] = useState<InitialValue>({
    invoiceId: 0,
    invoiceCode: '',
    projectCode: '',
    projectName: '',
    serviceId: 0,
    serviceCode: '',
    serviceName: '',
    clientCode: '',
    clientName: '',
    serviceStartDate: '',
    serviceEndDate: '',
    status: '',
    details: [{ ...blankDetail }],
    invoiceDate: '',
    orderId: 0,
    orderNumber: '',
    orderName: '',
    documents: [blankDocumentInfo],
    paymentDate: '',
  });
  const [listData, setListData] = useState<Detail[]>([{ ...blankDetail }]);

  const toggleMember = () => {
    setModalMember(!modalMember);
  };
  const [dragAndDrop, setDragAndDrop] = useState(initialDnDState);
  const [documents, setDocuments] = useState<invoiceDocument[]>([blankDocumentInfo]);
  const [prevDocuments, setPrevDocuments] = useState<invoiceDocument[]>([blankDocumentInfo]);
  const [preDocId, setPreDocId] = useState<number[]>([]);
  const [delDocId, setDelDocID] = useState<number[]>([]);
  const [orderDetailIds, setOrderDetailIds] = useState<number[]>([]);
  const prevState: { id?: number; from?: string; rows?: any[]; savedTemp?: boolean } = location.state
    ? (location.state as {
        id?: number;
        from?: string;
        rows?: any[];
        savedTemp?: boolean;
      })
    : {};
  const classNames = ['table-body row d-flex align-items-stretch'];
  const [deleteDetails, setDeleteDetails] = useState<number[]>([]);
  const onDragStart = (event: { currentTarget: { dataset: { position: number } } }, value: Detail[]) => {
    const initialPosition = Number(event.currentTarget.dataset.position);
    setDragAndDrop({
      ...dragAndDrop,
      draggedFrom: initialPosition,
      isDragging: true,
      originalOrder: value,
    });
  };

  const onDragOver = (event: React.DragEvent<HTMLDivElement>, formikProps: FormikProps<InitialValue>) => {
    event.preventDefault();

    let newList = dragAndDrop.originalOrder;

    const { draggedFrom } = dragAndDrop;

    const draggedTo = Number(event.currentTarget.dataset.position);

    const itemDragged = newList[draggedFrom];
    const remainingItems = newList.filter((item, index) => index !== draggedFrom);

    newList = [...remainingItems.slice(0, draggedTo), itemDragged, ...remainingItems.slice(draggedTo)];

    if (draggedTo !== dragAndDrop.draggedTo) {
      const updateDetailIdData: Detail[] = [];
      newList.forEach((n, i) => {
        updateDetailIdData.push({ ...newList[i], invoiceDetailId: formikProps.values.details[i].invoiceDetailId });
      });
      setDragAndDrop({
        ...dragAndDrop,
        updatedOrder: updateDetailIdData,
        draggedTo,
      });
    }
  };

  const onDrop = (formikProps: FormikProps<InitialValue>) => {
    formikProps.setFieldValue('details', [...dragAndDrop.updatedOrder]);
    setDragAndDrop({
      ...dragAndDrop,
      draggedFrom: -1,
      draggedTo: -1,
      isDragging: false,
    });
  };

  const onDragLeave = () => {
    setDragAndDrop({
      ...dragAndDrop,
      draggedTo: 0,
    });
  };

  const initData: InitialValue = id
    ? {
        ...initFindData,
        details: listData,
        invoiceDate: formatDate(initFindData.invoiceDate, 'yyyy-MM-dd'),
        paymentDate: formatDate(initFindData.paymentDate, 'yyyy-MM-dd'),
        documents,
      }
    : {
        ...initFindData,
        details: listData,
        status: Status.CREATING,
        invoiceDate: '',
        orderNumber: prevState.from === 's0011' ? '' : initFindData.orderNumber,
        orderName: prevState.from === 's0011' ? '' : initFindData.orderName,
        documents: [blankDocumentInfo],
        paymentDate: '',
      };
  useEffect(() => {
    setLoading(true);
    api.employeeGetAll().then((res) => {
      const memberList = res.data.map((member: { employeeCode: string; employeeName: string }) => ({
        value: member.employeeCode,
        label: member.employeeName,
      }));
      setMembersOptions(memberList);
    });
    if (!id) {
      setDetailFlg(!detailFlg);
    } else {
      setEditFlag(true);
    }
    // setData(mockData);
    setLoading(false);
  }, [history, location.state]);

  const handleSubmit = (values: InitialValue, actions: FormikHelpers<InitialValue>) => {
    const invoiceUpdateId: number[] = [];
    const submitInvoiceDetails: React.SetStateAction<any[]> = [];
    const prevArrDocId: number[] = prevDocuments
      ? prevDocuments.map((doc: invoiceDocument) => doc.documentId).filter(Boolean)
      : [];
    const arrDocId: number[] = values.documents
      ? values.documents.map((doc: invoiceDocument) => doc.documentId).filter(Boolean)
      : [];
    const createDocId: number[] = [];
    arrDocId.forEach((val) => {
      if (prevArrDocId.indexOf(val) <= -1) createDocId.push(val);
    });
    const invoiceDocCreateValue: InvoiceDocumentAttributes = {
      invoiceId: values.invoiceId,
      documents: createDocId,
    };
    const setInitMsg = () => {
      setTimeout(() => {
        setSavedAlert({ value: false, message: '' });
        setCreateConfirm(false);
        setSubmit(false);
      }, 3000);
    };
    const setMessage = (statusValue: string) => {
      if (statusValue === Status.INVOICED) {
        setSavedAlert({ value: true, message: '' });
        setInitMsg();
      } else if (statusValue === Status.CREATING || statusValue === Status.INVOICE_CHANGED) {
        setSavedAlert({ value: true, message: '' });
        setInitMsg();
      }
      setStatus(statusValue);
    };
    if (id) {
      if (values.status === Status.INVOICED) {
        setCreateConfirm(true);
      }
      values.details.forEach((item, i: number) =>
        submitInvoiceDetails.push({
          invoiceDetailId: item.invoiceDetailId ? item.invoiceDetailId : null,
          index: i + 1,
          invoiceId: id,
          note: item.person ? item.person : item.note,
          person: item.person ? item.person : null,
          quantity: item.quantity ? item.quantity : null,
          unit: item.unit ? item.unit : null,
          unitPrice: item.unitPrice ? item.unitPrice : null,
          price:
            // eslint-disable-next-line no-nested-ternary
            item.quantity && item.unitPrice
              ? item.remarks === Remark.DISCOUNT
                ? -Math.abs(item.quantity * item.unitPrice)
                : item.quantity * item.unitPrice
              : null,
          remarks: item.remarks,
        })
      );

      const updateInvoiceValue: InvoiceUpdateAttributes = {
        invoiceCode: values.invoiceCode,
        invoiceDate: values.invoiceDate,
        status: values.status,
        paymentDate: values.paymentDate,
        orderId: values.orderId,
        details: submitInvoiceDetails,
        deleteDetails,
      };
      if (submit) {
        api.invoiceUpdate(id, updateInvoiceValue).then(() => {
          api.updateInvoiceFlag(true, invoiceUpdateId);
          delDocId.forEach((delId) => {
            api.deleteInvoiceDocument(delId).then(() => {
              api.deleteFile(delId);
            });
          });
          if (invoiceDocCreateValue.documents.length > 0) {
            api.createInvoiceDocument({ ...invoiceDocCreateValue, invoiceId: id });
          }
          setMessage(String(updateInvoiceValue.status));
        });
      }
      history.push(location.pathname);
    } else {
      if (prevState.rows) {
        prevState.rows.forEach((item) => {
          if (item.orderDetailId) {
            invoiceUpdateId.push(item.orderDetailId);
          }
        });
      }

      if (values.status === Status.INVOICED) {
        setCreateConfirm(true);
      }
      values.details.forEach((item: Detail, i: number) => {
        submitInvoiceDetails.push({
          index: i + 1,
          invoiceId: 0,
          note: item.person ? item.person : item.note,
          person: item.person ? item.person : null,
          quantity: item.quantity ? item.quantity : null,
          unit: item.unit ? item.unit : null,
          unitPrice: item.unitPrice ? item.unitPrice : null,
          price:
            // eslint-disable-next-line no-nested-ternary
            item.quantity && item.unitPrice
              ? item.remarks === Remark.DISCOUNT
                ? -Math.abs(item.quantity * item.unitPrice)
                : item.quantity * item.unitPrice
              : null,
          remarks: item.remarks,
        });
      });

      const createEstimateValue: EstimateCreateAttributes = {
        serviceId: values.serviceId,
        estimateCode: values.invoiceCode.trim(),
        status: '',
        estimateDate: values.invoiceDate,
        details: [],
      };

      const createInvoiceValue: InvoiceCreateAttributes = {
        invoiceCode: values.invoiceCode.trim(),
        invoiceDate: values.invoiceDate,
        status: values.status,
        paymentDate: values.paymentDate,
        orderId: values.orderId,
        details: submitInvoiceDetails,
      };
      if (submit) {
        if (values.orderId === undefined || values.orderId === 0) {
          api.estimatePost(createEstimateValue).then((estimate) => {
            const { estimateId } = estimate.data as any;
            const createOrderValue: OrderCreateAttributes = {
              orderCode: padId(values.serviceCode?.trim(), 12),
              estimateId,
              status: '',
              orderDate: values.invoiceDate,
              orderNumber: values.orderNumber !== '' ? padId(values.orderNumber?.trim(), 12) : '',
              orderName: values.orderName !== '' ? values.orderName?.trim() : '',
              description: '',
              details: [],
            };
            api.orderPost(createOrderValue).then((order) => {
              const { orderId } = order.data as any;
              api.invoicePost({ ...createInvoiceValue, orderId }).then((invoice) => {
                const { invoiceId } = invoice.data as any;
                delDocId.forEach((delId) => {
                  api.deleteFile(delId).then(() => {
                    api.deleteInvoiceDocument(delId);
                  });
                });
                if (invoiceDocCreateValue.documents.length > 0) {
                  api.createInvoiceDocument({ ...invoiceDocCreateValue, invoiceId });
                }
                setMessage(String(createInvoiceValue.status));
                history.push({
                  pathname: `${ScreenUrl.INVOICE_UPDATE}${invoiceId}`,
                  state: invoiceUpdateId,
                });
              });
            });
          });
        } else {
          api.invoicePost(createInvoiceValue).then((invoice) => {
            const { invoiceId } = invoice.data as any;
            api.updateInvoiceFlag(true, invoiceUpdateId);
            if (invoiceDocCreateValue.documents.length > 0) {
              api.createInvoiceDocument({ ...invoiceDocCreateValue, invoiceId });
            }
            setMessage(String(createInvoiceValue.status));
            history.push({
              pathname: `${ScreenUrl.INVOICE_UPDATE}${invoiceId}`,
            });
          });
        }
      }
    }
  };
  const fetchData = async () => {
    if (location.state) {
      if (prevState.from === 's0013' && prevState.id) {
        await api.getOrderById(prevState.id).then((res) => {
          const resData = res.data as any;
          const invoiceDetails: InvoiceDetailAttributes[] = prevState.rows ? prevState.rows : [];
          setInitFindData({
            ...initFindData,
            ...resData,
            details: invoiceDetails,
            invoiceCode: resData.orderCode,
          });
          setListData([...invoiceDetails]);
          if (prevState.savedTemp && prevState.rows && prevState.rows.length > 0) {
            const oldDetails = prevState.rows;
            const savedTempValue = {
              invoiceCode: resData.orderCode.trim(),
              invoiceDate: initFindData.invoiceDate,
              status: Status.CREATING,
              paymentDate: initFindData.paymentDate,
              orderId: resData.orderId,
              details: invoiceDetails,
            };
            const oldDetailUpdateId: number[] = [];
            oldDetails.forEach((o) => {
              oldDetailUpdateId.push(o.orderDetailId);
            });
            api.updateInvoiceFlag(true, oldDetailUpdateId).then(() => {
              api.invoicePost(savedTempValue).then((invoice) => {
                const savedTempInvoiceId = (invoice.data as any).invoiceId;
                history.push({
                  pathname: `${ScreenUrl.INVOICE_UPDATE}${savedTempInvoiceId}`,
                });
                setSavedTempAlert({ value: true, message: '' });
                setTimeout(() => {
                  setSavedTempAlert({ value: false, message: '' });
                }, 3000);
              });
            });
          }
        });
        setEditFlag(true);
      } else if (prevState.from === 's0011' && prevState.id) {
        await api.getServiceById(prevState.id).then((res) => {
          const resData = res.data as any;
          setInitFindData({ ...initFindData, ...resData, invoiceCode: padId(resData.serviceId, 12) });
        });
        setEditFlag(false);
      }
      setStatus(initData.status);
    }
    if (id) {
      api.getInvoiceById(id).then((res) => {
        const resData = res.data as any;
        if (resData.Order) {
          const dataLink: linkScreen[] = [];
          if (resData.Order.Estimate.estimateStatus.trim() !== '') {
            dataLink.push({
              name: '',
              link: `${ScreenUrl.ESTIMATE_UPDATE}`,
              data: {
                id: resData.Order.Estimate.estimateId,
                code: resData.Order.Estimate.estimateCode,
                status: resData.Order.Estimate.estimateStatus,
              },
            });
          }
          if (resData.Order.orderStatus.trim() !== '') {
            dataLink.push({
              name: '',
              link: `${ScreenUrl.ORDER_UPDATE}`,
              data: { id: resData.Order.orderId, code: resData.Order.orderCode, status: resData.Order.orderStatus },
            });
          }
          setLinkData(dataLink);
        }
        setInitFindData({
          ...initFindData,
          ...resData,
          orderNumber: resData.orderNumber,
          orderName: resData.orderName,
          details: resData.details,
        });
        // setPrevInvoiceDetail(resData.details);
        setListData(resData.details ? [...resData.details] : []);
        setStatus(resData.status);
      });
      const documentRes = await api.getInvoiceDocuments(id);
      const documentData = [...documentRes.data, blankDocumentInfo];
      setDocuments(documentData);
      setPrevDocuments(documentData);
      const prevArrDocId: number[] = documentData
        ? documentData.map((doc: invoiceDocument) => doc.documentId).filter(Boolean)
        : [];
      setPreDocId(prevArrDocId);
    }
    setLoading(false);
  };
  useEffect(() => {
    setLoading(true);
    fetchData();
  }, [id, location.state, savedAlert.value]);
  const [detailFlg, setDetailFlg] = useState(false);
  const callDeleteInvoice = (act: boolean, redirect: string) => {
    if (act) {
      api.deleteInvoice(id).then(() => {
        api.updateInvDetailDelFlg(id).then((res) => {
          const arrInvoice = res.data as any;
          const delOrderIds: number[] = arrInvoice
            ? arrInvoice.map((invoiceDetail: any) => invoiceDetail.orderDetailId).filter(Boolean)
            : [];
          api.updateInvoiceFlag(false, delOrderIds);
        });
        history.push(redirect, true);
      });
    }
  };

  return (
    <AppLayout>
      <Formik
        initialValues={initData}
        onSubmit={(values, actions) => handleSubmit(values, actions)}
        validationSchema={schema}
        validate={(values) => {
          if (values.status !== Status.CREATING) {
            setSchema(SchemaValidation);
          } else setSchema(draftSchemaValidation);
        }}
        enableReinitialize
      >
        {(formikProps) => (
          <Form>
            {!noScroll && Object.keys(formikProps.errors).length > 0 && window.scrollTo(0, 0)}
            {linkData?.length > 0 && (
              <CustomSidebar
                element={
                  <>
                    {linkData.map((item, index) => (
                      <div
                        key={`linkData-${linkData.length + index - linkData.length}`}
                        className="mb-4"
                        style={{ borderBottom: '1px solid whitesmoke' }}
                      >
                        <h6>{item.name}</h6>
                        <div className="form-inline">
                          <label className="col-sm-3 pl-0 pr-0">{item.name}:</label>
                          <Link
                            to={{ pathname: `${item.link}${item.data.id}`, state: 's0014' }}
                            className="col-sm-5 pl-0 pr-0"
                          >
                            {item.data.code ? padId(item.data.code, 12) : ''}
                          </Link>
                          <div className="col-sm-4 pl-0 pr-0">
                            {item.data.status !== '' ? STATUS_MAP[item.data.status] : ''}
                          </div>
                        </div>
                      </div>
                    ))}
                  </>
                }
              />
            )}
            <Breadcrumb
              parent=""
              title=""
              bookmarkComponent={
                id && (
                  <div className="bookmark pull-right">
                    <Row>
                      <Button
                        className="mr-3"
                        variant="info"
                        onClick={() => {
                          history.push({
                            pathname: `${ScreenUrl.WORK_REPORT}${formikProps.values.invoiceId}`,
                            state: formikProps.values.invoiceId,
                          });
                        }}
                      >
                        
                      </Button>
                      <Button
                        variant="info"
                        className="mr-1"
                        onClick={() => {
                          setDeleteConfirm(!deleteConfirm);
                        }}
                      >
                        <i className="icofont icofont-trash" />
                      </Button>
                    </Row>
                  </div>
                )
              }
            />
            <Card>
              <CardHeader>
                <h3></h3>
                {savedAlert.value && window.scrollTo(0, 0)}
                <Alert isOpen={savedAlert.value || savedTempAlert.value}>
                  {savedAlert.message || savedTempAlert.message}
                </Alert>
                {deleteConfirm && (
                  <ConfirmModal
                    isOpen={deleteConfirm}
                    modalProps={{
                      modalTitle: '',
                      message: '?',
                      yesTitle: '',
                      noTitle: '',
                      functionOnYes: () => callDeleteInvoice(true, `${ScreenUrl.ORDER_MANAGEMENT}`),
                    }}
                  />
                )}
                {createConfirm && (
                  <ConfirmModal
                    isOpen={createConfirm}
                    setIsOpen={setCreateConfirm}
                    modalProps={{
                      modalTitle: '',
                      message: '?',
                      yesTitle: '',
                      noTitle: '',
                      functionOnYes: () => {
                        setSubmit(true);
                        formikProps.submitForm().then(() => window.scrollTo(0, 0));
                      },
                    }}
                  />
                )}
              </CardHeader>
              <CardBody className="tabbed-card">
                {modalMember && (
                  <CreateEmployee
                    parentFormikProps={formikProps}
                    currentIndex={currentIndex}
                    dataProps={formikProps.values.details[currentIndex]?.person}
                    isOpen={modalMember}
                    memberOptions={membersOptions}
                  />
                )}
                <Card className="b-r-0" style={{ boxShadow: 'none' }}>
                  <Row className="custom-form-group form-inline">
                    <Col className="col-sm-6 col-md-5 col-lg-6 pl-5">
                      <Row style={{ alignItems: 'center' }}>
                        <label className="col-sm-4 pl-0 pr-1" style={{ justifyContent: 'left' }}>
                          
                        </label>
                        <Col className="col-sm-8 pl-0">
                          <span>
                            {formikProps.values.projectCode} {formikProps.values.projectName}
                          </span>
                        </Col>
                      </Row>
                    </Col>
                    <Col className="col-sm-6 col-md-5 offset-md-2 col-lg-6 offset-lg-0 pl-5">
                      <Row style={{ alignItems: 'center' }}>
                        <label className="col-sm-4 pl-1 pr-0" style={{ justifyContent: 'left' }}>
                          
                        </label>
                        <Col className="col-sm-8 pl-0">
                          <span className="col-sm-4 pl-0 pr-4">{formatDate(formikProps.values.serviceStartDate)}</span>
                          {formikProps.values.serviceStartDate || formikProps.values.serviceEndDate ? (
                            <span>~</span>
                          ) : null}
                          <span className="col-sm-4 pr-0 pl-4">{formatDate(formikProps.values.serviceEndDate)}</span>
                        </Col>
                      </Row>
                    </Col>
                  </Row>
                  <Row className="custom-form-group form-inline">
                    <Col className="col-sm-6 col-md-5 col-lg-6 pl-5">
                      <Row style={{ alignItems: 'center' }}>
                        <label className="col-sm-4 pl-0 pr-1" style={{ justifyContent: 'left' }}>
                          
                        </label>
                        <Col className="col-sm-8 pl-0">
                          <span>
                            {formikProps.values.serviceCode} {formikProps.values.serviceName}
                          </span>
                        </Col>
                      </Row>
                    </Col>
                  </Row>
                  <Row className="custom-form-group form-inline">
                    <Col className="col-sm-6 col-md-5 col-lg-6 pl-5">
                      <Row style={{ alignItems: 'center' }}>
                        <label className="col-sm-4 pl-0 pr-1" style={{ justifyContent: 'left' }}>
                          
                        </label>
                        <Col className="col-sm-8 pl-0">
                          <span>{formikProps.values.clientName}</span>
                        </Col>
                      </Row>
                    </Col>
                    <Col className="col-sm-6 col-md-5 offset-md-2 col-lg-6 offset-lg-0 pl-5">
                      <Row style={{ alignItems: 'center' }}>
                        <label className="col-sm-4 pl-1 pr-0" style={{ justifyContent: 'left' }}>
                          
                        </label>
                        <div className="form-inline col-sm-8 pl-0">
                          <span>{STATUS_MAP[formikProps.values.status]}</span>
                        </div>
                      </Row>
                    </Col>
                  </Row>
                  <Row className="custom-form-group form-inline">
                    <Col className="col-sm-6 col-md-5 col-lg-6 pl-5">
                      <Row style={{ alignItems: 'center' }}>
                        <label className="col-sm-4 pl-0 pr-1 required" style={{ justifyContent: 'left' }}>
                          
                        </label>
                        <Col className="col-sm-8 pl-0">
                          <Input className="form-control" type="text" name="invoiceCode" style={{ width: '100%' }} />
                        </Col>
                      </Row>
                    </Col>
                    <Col className="col-sm-6 col-md-5 offset-md-2 col-lg-6 offset-lg-0 pl-5">
                      <Row style={{ alignItems: 'center' }}>
                        <label className="col-sm-4 pl-0 pr-1 required" style={{ justifyContent: 'left' }}>
                          
                        </label>
                        <Col className="col-sm-8 pl-0">
                          <Input className="form-control w-50" type="date" name="invoiceDate" />
                        </Col>
                      </Row>
                    </Col>
                  </Row>
                  <Row className="custom-form-group form-inline">
                    <Col className="col-sm-6 col-md-5 offset-md-2 col-lg-6 offset-lg-0 pl-5">
                      <Row style={{ alignItems: 'center' }}>
                        <label className="col-sm-4 pl-0 pr-1 required" style={{ justifyContent: 'left' }}>
                          
                        </label>
                        <Col className="col-sm-8 pl-0">
                          <Input className="form-control w-50" type="date" name="paymentDate" />
                        </Col>
                      </Row>
                    </Col>
                  </Row>
                  <Row className="custom-form-group form-inline">
                    <Col className="col-sm-6 col-md-5 col-lg-6 pl-5">
                      <Row style={{ alignItems: 'center' }}>
                        <label className="col-sm-4 pl-0 pr-1" style={{ justifyContent: 'left' }}>
                          
                        </label>
                        {prevState.from === 's0011' ? (
                          <Col className="col-sm-8 pl-0">
                            <Input className="form-control" type="text" name="orderNumber" style={{ width: '100%' }} />
                          </Col>
                        ) : (
                          <Col className="col-sm-8 pl-0">
                            <span>{formikProps.values.orderNumber ? formikProps.values.orderNumber.trim() : ''}</span>
                          </Col>
                        )}
                      </Row>
                    </Col>
                    <Col className="col-sm-6 col-md-5 offset-md-2 col-lg-6 offset-lg-0 pl-5">
                      <Row style={{ alignItems: 'center' }}>
                        <label className="col-sm-4 pl-0 pr-1" style={{ justifyContent: 'left' }}>
                          
                        </label>
                        {prevState.from === 's0011' ? (
                          <Col className="col-sm-8 pl-0">
                            <Input className="form-control" type="text" name="orderName" style={{ width: '100%' }} />
                          </Col>
                        ) : (
                          <Col className="col-sm-8 pl-0">
                            <span>{formikProps.values.orderName}</span>
                          </Col>
                        )}
                      </Row>
                    </Col>
                  </Row>
                </Card>
                <Card className="b-r-0" style={{ boxShadow: 'none' }}>
                  <CardHeader
                    style={{
                      padding: '7px',
                      backgroundColor: '#ddd',
                      borderTopLeftRadius: 0,
                      borderTopRightRadius: 0,
                    }}
                  >
                    <h6 style={{ marginBottom: '0px' }}></h6>
                  </CardHeader>
                  <CardBody>
                    <div>
                      {detailFlg ? (
                        <FieldArray name="details">
                          {(arrayHelper) => (
                            <div style={{ width: '950px', paddingRight: '20px' }}>
                              <div className="table table-striped">
                                <div
                                  className="row font-weight-bold pt-3 pb-3"
                                  style={{ paddingLeft: '5px', paddingRight: '5px' }}
                                >
                                  <div style={{ width: '3%' }} />
                                  <div style={{ width: '22%' }} className="ml-1">
                                    
                                  </div>
                                  <div style={{ width: '7%' }} className="ml-1">
                                    
                                  </div>
                                  <div style={{ width: '8%' }} className="ml-1">
                                    
                                  </div>
                                  <div style={{ width: '12%' }} className="ml-1">
                                    
                                  </div>
                                  <div style={{ width: '18%' }} className="ml-1">
                                    
                                  </div>
                                  <div style={{ width: '20%' }} className="ml-1">
                                    
                                  </div>
                                  <div style={{ width: '6%', padding: '0 9px' }}>
                                    <i
                                      className="icofont icofont-ui-add pr-2"
                                      onClick={() => {
                                        setNoScroll(true);
                                        arrayHelper.insert(0, editFlag ? { ...textDetail } : { ...blankDetail });
                                      }}
                                    />
                                  </div>
                                </div>
                                {formikProps.values.details?.map((item, index: number) => (
                                  <div
                                    key={`detail-${
                                      formikProps.values.details.length + index - formikProps.values.details.length
                                    }`}
                                    id={String(index)}
                                    data-position={index}
                                    draggable
                                    // eslint-disable-next-line @typescript-eslint/ban-ts-comment
                                    // @ts-ignore
                                    onDragStart={(e) => onDragStart(e, formikProps.values.details)}
                                    onDragOver={(e) => onDragOver(e, formikProps)}
                                    onDrop={() => onDrop(formikProps)}
                                    onDragLeave={onDragLeave}
                                    className={
                                      dragAndDrop && dragAndDrop.draggedTo === Number(index)
                                        ? classNames.join(' dropArea')
                                        : classNames.join('')
                                    }
                                  >
                                    <div style={{ width: '3%', paddingTop: '9px' }} className="text-right">
                                      {index + 1}
                                    </div>
                                    <div style={{ width: '22%' }} className="ml-1 form-inline">
                                      {item.remarks === Remark.MEMBER ? (
                                        <>
                                          <div style={{ width: '90%' }}>
                                            <CustomSelect
                                              name={`details[${index}].person`}
                                              onChange={(value) =>
                                                formikProps.setFieldValue(
                                                  `details[${index}].person`,
                                                  membersOptions.filter((member) => member.value === value)[0].value
                                                )
                                              }
                                              options={membersOptions}
                                              isDisabled={editFlag}
                                            />
                                          </div>
                                          <i
                                            style={{ width: '10%' }}
                                            className="pointer icofont icofont-ui-add pl-1"
                                            onClick={() => {
                                              setCurrentIndex(index);
                                              formikProps.setFieldValue(
                                                `details[${index}].person`,
                                                membersOptions.slice(-1)[0]?.value
                                                  ? padId(Number(membersOptions.slice(-1)[0].value) + 1, 12)
                                                  : padId(1, 12)
                                              );
                                              toggleMember();
                                            }}
                                            hidden={editFlag}
                                          />
                                        </>
                                      ) : (
                                        <Input
                                          name={`details[${index}].note`}
                                          style={{ width: '90%' }}
                                          disabled={editFlag && item.remarks !== Remark.TEXT}
                                        />
                                      )}
                                    </div>
                                    <div style={{ width: '7%' }} className="ml-1">
                                      <Input
                                        type="number"
                                        min={0}
                                        name={`details[${index}].quantity`}
                                        className="text-right"
                                        onValueChange={(value) =>
                                          item.remarks === Remark.DISCOUNT
                                            ? formikProps.setFieldValue(
                                                `details[${index}].price`,
                                                -Math.abs(item.unitPrice ? Number(value) * item.unitPrice : 0)
                                              )
                                            : formikProps.setFieldValue(
                                                `details[${index}].price`,
                                                item.unitPrice ? Number(value) * item.unitPrice : 0
                                              )
                                        }
                                        disabled={item.remarks === Remark.TEXT || editFlag}
                                      />
                                    </div>
                                    <div style={{ width: '8%' }} className="ml-1">
                                      <Input
                                        name={`details[${index}].unit`}
                                        disabled={item.remarks === Remark.TEXT || editFlag}
                                      />
                                    </div>
                                    <div style={{ width: '12%' }} className="ml-1">
                                      <Input
                                        type="number"
                                        min={0}
                                        name={`details[${index}].unitPrice`}
                                        className="text-right"
                                        onValueChange={(value) =>
                                          item.remarks === Remark.DISCOUNT
                                            ? formikProps.setFieldValue(
                                                `details[${index}].price`,
                                                -Math.abs(item.quantity ? item.quantity * Number(value) : ZERO)
                                              )
                                            : formikProps.setFieldValue(
                                                `details[${index}].price`,
                                                item.quantity ? item.quantity * Number(value) : ZERO
                                              )
                                        }
                                        disabled={item.remarks === Remark.TEXT || editFlag}
                                      />
                                    </div>
                                    <div
                                      style={{
                                        width: '18%',
                                        color: item.remarks === Remark.DISCOUNT ? 'red' : undefined,
                                        backgroundColor:
                                          item.remarks === Remark.TEXT || editFlag ? '#e9ecef' : undefined,
                                        opacity: item.remarks === Remark.TEXT ? 1 : undefined,
                                      }}
                                      className="form-control text-right ml-1"
                                    >
                                      {item.remarks === Remark.DISCOUNT && ' '}
                                      {item.quantity && item.unitPrice
                                        ? formatPrice(item.quantity * item.unitPrice)
                                        : ''}
                                    </div>
                                    <div style={{ width: '20%' }} className="ml-1">
                                      <CustomSelect
                                        name={`details[${index}].remarks`}
                                        options={remarksOptions}
                                        onChange={(value) => {
                                          formikProps.setFieldValue(
                                            `details[${index}].price`,
                                            item.quantity && item.unitPrice ? item.quantity * item.unitPrice : ZERO
                                          );
                                          if (value === Remark.TEXT) {
                                            formikProps.setFieldValue(`details[${index}].quantity`, ZERO);
                                            formikProps.setFieldValue(`details[${index}].unit`, '');
                                            formikProps.setFieldValue(`details[${index}].unitPrice`, ZERO);
                                          } else if (value === Remark.DISCOUNT) {
                                            formikProps.setFieldValue(
                                              `details[${index}].price`,
                                              -Math.abs(
                                                item.quantity && item.unitPrice ? item.quantity * item.unitPrice : 0
                                              )
                                            );
                                          }
                                        }}
                                        isDisabled={editFlag}
                                      />
                                    </div>
                                    <div style={{ width: '6%', paddingTop: '9px' }}>
                                      <div className="form-inline pointer pl-2">
                                        <i
                                          className="icofont icofont-ui-add pr-2"
                                          onClick={() => {
                                            setNoScroll(true);
                                            arrayHelper.insert(
                                              index + 1,
                                              editFlag ? { ...textDetail } : { ...blankDetail }
                                            );
                                          }}
                                          hidden={editFlag && item.remarks !== Remark.TEXT}
                                        />
                                        {formikProps.values.details.length === 1 && index === 0 ? (
                                          <i
                                            className="icofont icofont-trash"
                                            onClick={(e) => {
                                              e.preventDefault();
                                              arrayHelper.remove(index);
                                              if (item.invoiceDetailId) {
                                                setDeleteDetails([...deleteDetails, item.invoiceDetailId]);
                                              }
                                              setNoScroll(true);
                                              arrayHelper.insert(index + 1, { ...blankDetail });
                                            }}
                                            hidden={editFlag && item.remarks !== Remark.TEXT}
                                          />
                                        ) : (
                                          <i
                                            className="icofont icofont-trash"
                                            onClick={(e) => {
                                              e.preventDefault();
                                              arrayHelper.remove(index);
                                              if (item.invoiceDetailId) {
                                                setDeleteDetails([...deleteDetails, item.invoiceDetailId]);
                                              }
                                            }}
                                            hidden={editFlag && item.remarks !== Remark.TEXT}
                                          />
                                        )}
                                      </div>
                                    </div>
                                  </div>
                                ))}
                              </div>
                            </div>
                          )}
                        </FieldArray>
                      ) : (
                        <DataTable
                          style={{ width: '950px', paddingRight: '20px' }}
                          onRowClicked={() => setDetailFlg(!detailFlg)}
                          columns={columns(membersOptions)}
                          data={formikProps.values.details}
                          pagination={false}
                          noHeader
                          subHeaderAlign="left"
                          loading={loading}
                          persistTableHead
                        />
                      )}
                    </div>
                  </CardBody>
                  <CardFooter>
                    {detailFlg && (
                      <Row>
                        <Button
                          variant="info"
                          type="button"
                          onClick={() => {
                            setDetailFlg(!detailFlg);
                            setLoading(false);
                            setNoScroll(true);
                          }}
                        >
                          
                        </Button>
                      </Row>
                    )}
                    <Row>
                      <Col className="form-group form-inline col-lg-group" />
                      <Col className="form-group form-inline col-lg-group">
                        <Col className="col-sm-5" />
                        <Col className="col-sm-3">
                          <span>:</span>
                        </Col>
                        <Col
                          className="col-sm-4"
                          style={{
                            textAlign: 'right',
                            color:
                              formikProps.values.details?.reduce(
                                (sum: number, item) => (item.price ? sum + item.price : sum),
                                0
                              ) < 0
                                ? 'red'
                                : undefined,
                          }}
                        >
                          {formikProps.values.details?.reduce(
                            (sum: number, item) => (item.price ? sum + item.price : sum),
                            0
                          ) < 0 && ' '}
                          {formatPrice(
                            Math.abs(
                              formikProps.values.details?.reduce(
                                (sum: number, item) => (item.price ? sum + item.price : sum),
                                0
                              )
                            )
                          )}
                        </Col>
                      </Col>
                    </Row>
                    <Row>
                      <Col className="form-group form-inline col-lg-group" />
                      <Col className="form-group form-inline col-lg-group">
                        <Col className="col-sm-5" />
                        <Col className="col-sm-3">
                          <span>:</span>
                        </Col>
                        <Col
                          className="col-sm-4"
                          style={{
                            textAlign: 'right',
                            color:
                              formikProps.values.details?.reduce(
                                (sum: number, item) => (item.price ? sum + item.price : sum),
                                0
                              ) *
                                0.1 <
                              0
                                ? 'red'
                                : undefined,
                          }}
                        >
                          {formikProps.values.details?.reduce(
                            (sum: number, item) => (item.price ? sum + item.price : sum),
                            0
                          ) *
                            0.1 <
                            0 && ' '}
                          {formatPrice(
                            Math.abs(
                              formikProps.values.details?.reduce(
                                (sum: number, item) => (item.price ? sum + item.price : sum),
                                0
                              ) * 0.1
                            )
                          )}
                        </Col>
                      </Col>
                    </Row>
                    <Row>
                      <Col className="form-group form-inline col-lg-group" />
                      <Col className="form-group form-inline col-lg-group">
                        <Col className="col-sm-5" />
                        <Col className="col-sm-3">
                          <span>:</span>
                        </Col>
                        <Col
                          className="col-sm-4"
                          style={{
                            textAlign: 'right',
                            color:
                              formikProps.values.details?.reduce(
                                (sum: number, item) => (item.price ? sum + item.price : sum),
                                0
                              ) +
                                formikProps.values.details?.reduce(
                                  (sum: number, item) => (item.price ? sum + item.price : sum),
                                  0
                                ) *
                                  0.1 <
                              0
                                ? 'red'
                                : undefined,
                          }}
                        >
                          {formikProps.values.details?.reduce(
                            (sum: number, item) => (item.price ? sum + item.price : sum),
                            0
                          ) +
                            formikProps.values.details?.reduce(
                              (sum: number, item) => (item.price ? sum + item.price : sum),
                              0
                            ) *
                              0.1 <
                            0 && ' '}
                          {formatPrice(
                            Math.abs(
                              formikProps.values.details?.reduce(
                                (sum: number, item) => (item.price ? sum + item.price : sum),
                                0
                              ) +
                                formikProps.values.details?.reduce(
                                  (sum: number, item) => (item.price ? sum + item.price : sum),
                                  0
                                ) *
                                  0.1
                            )
                          )}
                        </Col>
                      </Col>
                    </Row>
                  </CardFooter>
                </Card>
                <Card className="b-r-0" style={{ boxShadow: 'none' }}>
                  <CardHeader
                    style={{
                      padding: '7px',
                      backgroundColor: '#ddd',
                      borderTopLeftRadius: 0,
                      borderTopRightRadius: 0,
                    }}
                  >
                    <h6 style={{ marginBottom: '0px' }}></h6>
                  </CardHeader>
                  <CardBody>
                    <div className="form-group">
                      <Row>
                        <Col className="form-group ">
                          <FieldArray name="documents">
                            {(arrayHelper) => (
                              <>
                                {formikProps.values?.documents
                                  ? formikProps.values.documents?.map((document, index) => (
                                      <Row key={`serviceDocument-${index + 1}`}>
                                        <Col className="form-group ">
                                          <DocumentUploader
                                            setNoScroll
                                            name={`documents[${index}].documentId`}
                                            documentName={document.documentName}
                                            documentId={document.documentId}
                                            filePath={document.filePath}
                                            filesInfo={document.filesInfo}
                                            deleteDoc
                                            onSelect={(files: File) => {
                                              arrayHelper.replace(index, {
                                                ...document,
                                                filesInfo: files,
                                                documentName: files.name,
                                              });
                                            }}
                                            onUpload={(resDocId: number, resFilePath: string) => {
                                              arrayHelper.replace(index, {
                                                ...document,
                                                documentId: resDocId,
                                                filePath: resFilePath,
                                              });
                                              setNoScroll(true);
                                              arrayHelper.insert(index + 1, blankDocumentInfo);
                                            }}
                                            onDelete={(delId) => {
                                              arrayHelper.remove(index);
                                              if (delId) {
                                                delDocId.push(delId);
                                              }
                                            }}
                                            update={!!id}
                                            prevDoc={preDocId}
                                          />
                                        </Col>
                                      </Row>
                                    ))
                                  : ''}
                              </>
                            )}
                          </FieldArray>
                        </Col>
                      </Row>
                    </div>
                  </CardBody>
                  <CardFooter />
                </Card>
                <div className="custom-form-group form-inline" style={{ justifyContent: 'flex-end' }}>
                  <Button
                    variant="info"
                    type="submit"
                    className="mr-3"
                    onClick={() => {
                      setNoScroll(false);
                      if (status === Status.INVOICED || status === Status.INVOICE_CHANGED) {
                        formikProps.setFieldValue('status', Status.INVOICE_CHANGED);
                      } else {
                        formikProps.setFieldValue('status', Status.CREATING);
                      }
                      setSubmit(true);
                    }}
                  >
                    
                  </Button>
                  <Button
                    variant="info"
                    type="submit"
                    onClick={() => {
                      setNoScroll(false);
                      formikProps.setFieldValue('status', Status.INVOICED);
                    }}
                  >
                    
                  </Button>
                </div>
              </CardBody>
            </Card>
          </Form>
        )}
      </Formik>
    </AppLayout>
  );
};
export default Invoice;
