import React, { useState } from 'react';
import { Nav, NavItem, NavLink } from 'reactstrap';

const CustomSidebar = (props: any) => {
  const [openFlg, setOpenFlg] = useState(false);
  const customSidebar = props;
  const containClass = ['customizer-contain'];
  const linkClass = ['customizer-links'];
  if (openFlg) {
    containClass.push('open');
    linkClass.push('open');
  } else {
    containClass.slice(-1, 1);
    linkClass.slice(-1, 1);
  }

  return (
    <>
      <div className={linkClass.join(' ')}>
        <div className="nav flex-column nac-pills" aria-orientation="vertical">
          <Nav tabs className="tab-list-bottom border-tab-primary">
            <NavItem className="nav nav-tabs">
              <NavLink>
                <div className="settings">
                  <i className="icon-link" onClick={() => setOpenFlg(!openFlg)} />
                </div>
              </NavLink>
            </NavItem>
          </Nav>
        </div>
      </div>
      <div className={containClass.join(' ')}>
        <div className="tab-content">
          <div className="customizer-header">
            <i className="icon-close" onClick={() => setOpenFlg(!openFlg)} />
          </div>
          <div className="customizer-body custom-scrollbar">{customSidebar.element}</div>
        </div>
      </div>
    </>
  );
};

export default CustomSidebar;
