import React, { useEffect, useState } from 'react';
import { Row, Col, Card, CardBody, CardHeader, Button, Alert } from 'reactstrap';
import { useParams, useLocation } from 'react-router';
import { Form, Formik } from 'formik';
import * as Yup from 'yup';
import Breadcrumb from '../components/common/breadcrumb';
import AppLayout from '../components/common/layout';
import { formatDate } from '../components/common/formatter/formatter';
import { CustomSelect, Input, Textarea } from '../components/common/form-helper';
import api from '../utils/api_client';
import { WorkReportCreateAttributes, WorkReportUpdateAttributes } from '../api';
import { OptionsType } from '../components/common/detail-form-common';

interface InitialValue {
  title: string;
  orderNumber: string;
  orderName: string;
  serviceStartDate: string;
  serviceEndDate: string;
  clientCode: string;
  clientName: string;
  members: string[];
  workingTime: number;
  workInfo: string;
  relatedDocument: string;
}

/**
 * 
 */
const initialValues: InitialValue = {
  title: '',
  orderNumber: '',
  orderName: '',
  serviceStartDate: '',
  serviceEndDate: '',
  clientCode: '',
  clientName: '',
  members: [],
  workingTime: 160,
  workInfo: '',
  relatedDocument: '',
};

const SchemaValidation = Yup.object().shape({
  title: Yup.string().nullable().max(267),
  workingTime: Yup.number().nullable().required(),
  workInfo: Yup.string().nullable().max(255),
  relatedDocument: Yup.string().nullable().max(255),
});

const WorkReport: React.FC = () => {
  // eslint-disable-next-line @typescript-eslint/ban-ts-comment
  // @ts-ignore
  const { id } = useParams();
  const location = useLocation();
  const [savedAlert, setSavedAlert] = useState<{ value: boolean; message: string }>({ value: false, message: '' });
  const [memberList, setMemberList] = useState<OptionsType[]>([]);
  const [initialData, setInitialData] = useState<InitialValue>(initialValues);

  const fetchData = async () => {
    // 
    api.employeeGetAll().then((res) => {
      const members = res.data.map((member: { employeeCode: string; employeeName: string }) => ({
        value: member.employeeCode,
        label: member.employeeName,
      }));
      setMemberList(members);
    });
  };

  useEffect(() => {
    fetchData().then();

    // 
    api.getWorkReportById(id).then((res) => {
      const resData = res.data as any;
      // 
      if (resData.workReportId) {
        // 
        if (resData.title === null) {
          resData.orderNumber = resData.orderNumber.trim();
          resData.orderName = resData.orderName.trim();
          // 
          if (resData.orderNumber && resData.orderName) {
            resData.title = `${resData.orderNumber}${resData.orderName}`;
          }
        }

        // null
        Object.keys(resData).map((key) => {
          if (resData[key] === null) {
            resData[key] = '';
          }
          return resData;
        });

        setInitialData({
          ...initialData,
          ...resData,
        });

        // 
      } else {
        const invoiceId: number = location.state as number;

        api.getWorkReportById(invoiceId).then((resInvoice) => {
          const resInvoiceData = resInvoice.data as any;
          // 
          if (resInvoiceData) {
            resInvoiceData.orderNumber = resInvoiceData.orderNumber.trim();
            resInvoiceData.orderName = resInvoiceData.orderName.trim();
            // 
            if (resInvoiceData.orderNumber && resInvoiceData.orderName) {
              resInvoiceData.title = `${resInvoiceData.orderNumber}${resInvoiceData.orderName}`;
            }

            // null
            Object.keys(resInvoiceData).map((key) => {
              if (resInvoiceData[key] === null) {
                resInvoiceData[key] = '';
              }
              return resInvoiceData;
            });

            setInitialData({
              ...initialData,
              ...resInvoiceData,
              invoiceId,
            });
          }
        });
      }
    });
  }, []);

  // 
  const handleSubmit = (values: any) => {
    // 
    if (values.workReportId) {
      const updateWorkReportValue: WorkReportUpdateAttributes = {
        workReportId: values.workReportId,
        invoiceId: values.invoiceId,
        title: values.title,
        workingTime: values.workingTime,
        workInfo: values.workInfo,
        relatedDocument: values.relatedDocument,
      };

      api.workReportUpdate(values.workReportId, updateWorkReportValue).then(() => {
        window.scrollTo(0, 0);
        setSavedAlert({ value: true, message: '' });
        setTimeout(() => {
          setSavedAlert({ value: false, message: '' });
        }, 3000);
      });
      // 
    } else {
      // work_report_idinvoice_code
      const newWorkReportId = values.invoiceId;
      const createWorkReportValue: WorkReportCreateAttributes = {
        workReportId: newWorkReportId,
        invoiceId: values.invoiceId,
        title: values.title,
        workingTime: values.workingTime,
        workInfo: values.workInfo,
        relatedDocument: values.relatedDocument,
      };

      api.workReportPost(createWorkReportValue).then(() => {
        window.scrollTo(0, 0);
        setSavedAlert({ value: true, message: '' });
        setTimeout(() => {
          setSavedAlert({ value: false, message: '' });
        }, 3000);
      });
    }
  };

  return (
    <AppLayout>
      <Formik
        initialValues={initialData}
        onSubmit={(values) => handleSubmit(values)}
        validationSchema={SchemaValidation}
        enableReinitialize
      >
        {(formikProps) => (
          <Form>
            <Breadcrumb
              parent=""
              title=""
              bookmarkComponent={
                <div className="bookmark pull-right mr-4">
                  <Button id="no-print" variant="info" className="mr-3" onClick={() => window.print()}>
                     <i className="icofont icofont-printer" />
                  </Button>
                </div>
              }
            />
            <Card id="section-to-print">
              <CardHeader>
                <h3></h3>
                <Alert isOpen={savedAlert.value}>{savedAlert.message}</Alert>
              </CardHeader>
              <CardBody className="tabbed-card">
                <Card className="b-r-0" style={{ boxShadow: 'none' }}>
                  <Row className="custom-form-group form-inline">
                    <Col className="col-sm-6 col-md-5 col-lg-6 pl-5">
                      <Row>
                        <label className="col-sm-3 pl-0 pr-1" style={{ justifyContent: 'left' }}>
                          
                        </label>
                        <Col className="col-sm-8 pl-0">
                          <Input className="w-100" type="text" name="title" />
                          {/* <span>
                            {formikProps.values.orderName}
                          </span> */}
                        </Col>
                      </Row>
                    </Col>
                    <Col className="col-sm-6 col-md-5 offset-md-2 col-lg-6 offset-lg-0 pl-5">
                      <Row style={{ alignItems: 'center' }}>
                        <label className="col-sm-3 pl-1 pr-0" style={{ justifyContent: 'left' }}>
                          
                        </label>
                        <Col className="col-sm-8 pl-0">
                          <span className="col-sm-4 pl-0 pr-4">{formatDate(formikProps.values.serviceStartDate)}</span>
                          {formikProps.values.serviceStartDate || formikProps.values.serviceEndDate ? (
                            <span>~</span>
                          ) : null}
                          <span className="col-sm-4 pr-0 pl-4">{formatDate(formikProps.values.serviceEndDate)}</span>
                        </Col>
                      </Row>
                    </Col>
                  </Row>
                  <Row className="custom-form-group form-inline">
                    <Col className="col-sm-6 col-md-5 col-lg-6 pl-5">
                      <Row>
                        <label className="col-sm-3 pl-0 pr-1" style={{ justifyContent: 'left' }}>
                          
                        </label>
                        <Col className="col-sm-8 pl-0">
                          <span>{formikProps.values.clientName}</span>
                        </Col>
                      </Row>
                    </Col>
                    <Col className="col-sm-6 col-md-5 offset-md-2 col-lg-6 offset-lg-0 pl-5">
                      <Row>
                        <label className="col-sm-3 pl-1 pr-0" style={{ justifyContent: 'left' }}>
                          
                        </label>
                        <Col className="col-sm-8 pl-0">
                          <CustomSelect name="members" options={memberList} isDisabled isMulti />
                        </Col>
                      </Row>
                    </Col>
                  </Row>
                  <Row className="custom-form-group form-inline">
                    <Col className="col-sm-6 col-md-5 col-lg-6 pl-5">
                      <Row>
                        <label className="col-sm-3 pl-0 pr-1 required" style={{ justifyContent: 'left' }}>
                          
                        </label>
                        <Col className="col-sm-8 pl-0">
                          <Input className="w-100" type="text" name="workingTime" />
                        </Col>
                      </Row>
                    </Col>
                    <Col className="col-sm-6 col-md-5 offset-md-2 col-lg-6 offset-lg-0 pl-5" />
                  </Row>
                </Card>
                <Card className="b-r-0" style={{ boxShadow: 'none', marginBottom: 0 }}>
                  <CardHeader
                    style={{ padding: '7px', backgroundColor: '#ddd', borderTopLeftRadius: 0, borderTopRightRadius: 0 }}
                  >
                    <h6 style={{ marginBottom: '0px' }}></h6>
                  </CardHeader>
                  <CardBody>
                    <Textarea name="workInfo" className="custom-textarea" />
                  </CardBody>
                </Card>
                <Card className="b-r-0" style={{ boxShadow: 'none', marginBottom: 0 }}>
                  <CardHeader
                    style={{ padding: '7px', backgroundColor: '#ddd', borderTopLeftRadius: 0, borderTopRightRadius: 0 }}
                  >
                    <h6 style={{ marginBottom: '0px' }}></h6>
                  </CardHeader>
                  <CardBody>
                    <Textarea name="relatedDocument" className="custom-textarea" />
                  </CardBody>
                </Card>
                <div className="custom-form-group form-inline" style={{ justifyContent: 'flex-end' }}>
                  <Button variant="info" type="submit" id="no-print">
                    
                  </Button>
                </div>
              </CardBody>
            </Card>
          </Form>
        )}
      </Formik>
    </AppLayout>
  );
};
export default WorkReport;
