import React, { useCallback, useEffect, useState } from 'react';
import querystring from 'querystring';
import { Row, Col, Button, Card, CardBody, CardHeader, UncontrolledCollapse, Alert } from 'reactstrap';
import { Form, Formik, FormikProps } from 'formik';
import { useHistory, useLocation } from 'react-router';
import DatePicker from 'react-datepicker';
import { Link } from 'react-router-dom';
import { Folder, List } from 'react-feather';
import { Badge } from 'react-bootstrap';
import Breadcrumb from '../components/common/breadcrumb';
import AppLayout from '../components/common/layout';
import { formatDate, formatPrice, padId } from '../components/common/formatter/formatter';
import { CustomSelect, Input } from '../components/common/form-helper';
import 'react-datepicker/dist/react-datepicker.css';
import { ScreenUrl, Status, STATUS_MAP } from '../constant/commonConstant';
import api from '../utils/api_client';
import DataTable from '../components/common/datatable/data-table';
import ExpandableComponents from '../components/order-management/estimate-expand';
import { OrderManagementListAttributes, ProjectServiceAttributes } from '../api';

interface InitialValue {
  projectCode: string;
  serviceCode: string;
  serviceName: string;
  clientCode: string;
  serviceStartDate: string;
  serviceEndDate: string;
  dataList: OrderManagementListAttributes[];
  projectOpen?: any;
  projectService: ProjectServiceAttributes[];
}

const blankProjectService: ProjectServiceAttributes[] = [
  {
    projectCode: '',
    projectName: '',
    Services: [{ serviceId: 0, serviceCode: '', serviceName: '' }],
  },
];

const OrderManagement: React.FC = () => {
  const history = useHistory();
  const location = useLocation();
  const [loading, setLoading] = useState(false);
  const [startDate, setStartDate] = useState(new Date());
  const [endDate, setEndDate] = useState(null);
  const [rangeDate, setRangeDate] = useState('');
  const [projectMode, setProjectMode] = useState(false);

  const [projectOptions, setProjectOptions] = useState<{ value: string; label: string }[]>([]);
  const [clientOptions, setClientOptions] = useState<{ value: string; label: string }[]>([]);
  const [serviceOptions, setServiceOptions] = useState<{ value: string; label: string }[]>([]);
  const [savedAlert, setSavedAlert] = useState<{ value: boolean; message: string }>({ value: false, message: '' });
  const [projectService, setProjectService] = useState<ProjectServiceAttributes[]>(blankProjectService);
  const projectOpen = new Array(projectService.length);

  const [initData, setInitData] = useState<InitialValue>({
    projectCode: '',
    serviceCode: '',
    serviceName: '',
    clientCode: '',
    serviceStartDate: '',
    serviceEndDate: '',
    dataList: [],
    projectOpen: {},
    projectService: [],
  });

  useEffect(() => {
    setLoading(true);
    api.projectGetAll().then((res) => {
      const projectList = res.data.map((project: { projectCode: string; projectName: string }) => ({
        value: project.projectCode,
        label: `${project.projectCode} ${project.projectName}`,
      }));
      setProjectOptions(projectList);
    });

    api.clientGetAll().then((res) => {
      const clientList = res.data.map((client) => ({
        value: client.clientCode,
        label: client.clientName,
      }));
      setClientOptions(clientList);
    });

    api.serviceGetAll().then((res) => {
      const serviceList = res.data.map((service) => ({
        value: service.serviceCode,
        label: `${service.serviceCode} ${service.serviceName}`,
      }));
      setServiceOptions(serviceList);
    });

    api.orderManagementListGet().then((orderManagements) => {
      if (orderManagements.data) {
        api.getProjectAndService().then((res) => {
          const filterData: ProjectServiceAttributes[] = [];
          if (res.data) {
            res.data.forEach((item) => {
              if (item.Services.length >= 1) {
                filterData.push(item);
              }
            });
            setProjectService(filterData);
            setInitData({ ...initData, dataList: orderManagements.data, projectService: filterData });
          }
        });
      }
    });
    setLoading(false);
    if (location.state) {
      setSavedAlert({ value: true, message: ' ' });
      setTimeout(() => {
        setSavedAlert({ value: false, message: '' });
      }, 3000);
    }
  }, []);

  const initIndex = new Array(initData.dataList.length);
  const [pIndex, setPIndex] = useState<{ index: number }[]>(initIndex);
  /**
   * stringURL
   * @param search 
   * @return 
   */
  const stringifySearchValues = useCallback(
    (values) => {
      const searchValues = {
        projectCode: values.projectCode,
        serviceCode: values.serviceCode,
        clientCode: values.clientCode,
        serviceStartDate: formatDate(values.serviceStartDate, 'yyyy-MM-dd'),
        serviceEndDate: formatDate(values.serviceEndDate, 'yyyy-MM-dd'),
      };
      const query = querystring.stringify(searchValues);
      history.replace({ search: query });
      api.orderManagementListGet({ query: searchValues }).then((res) => {
        setInitData({ ...initData, dataList: res.data });
      });
    },
    [history]
  );
  /**
   * 
   * @param values 
   * @return 
   */
  const handleSearchSubmit = useCallback(
    (values: any) => {
      stringifySearchValues(values);
    },
    [stringifySearchValues]
  );

  const CustomInput = (props: React.HTMLProps<HTMLInputElement>) => (
    // eslint-disable-next-line @typescript-eslint/ban-ts-comment
    // @ts-ignore
    // eslint-disable-next-line react/jsx-props-no-spreading
    <Input {...props} className="w-100" name="rangeDate" value={rangeDate} />
  );

  const childColumns = (formikProps: FormikProps<InitialValue>) => [
    {
      name: '',
      selector: 'serviceCode',
      sortable: true,
      style: { alignItems: 'center', paddingTop: '9px', paddingBottom: '9px' },
      minWidth: '240px',
      cell: (row: OrderManagementListAttributes) =>
        row.estimateFlg || row.orderFlg || row.invoiceFlg ? null : (
          <div>
            <Link to={`${ScreenUrl.SERVICE_UPDATE}${row.serviceId}`} className="text-link">
              {row.serviceCode}
            </Link>
            <div>{row.serviceName}</div>
          </div>
        ),
    },
    {
      name: '',
      selector: 'clientCode',
      sortable: true,
      style: { alignItems: 'center', paddingTop: '9px', paddingBottom: '9px' },
      minWidth: '150px',
      cell: (row: OrderManagementListAttributes) =>
        row.estimateFlg || row.orderFlg || row.invoiceFlg ? null : (
          <div>{clientOptions.filter((c) => c.value === row.clientCode)[0]?.label}</div>
        ),
    },
    {
      name: (
        <>
          <p className="mb-0"></p>
          <p></p>
        </>
      ),
      selector: 'serviceStartDate',
      sortable: true,
      style: { alignItems: 'center', paddingTop: '9px', paddingBottom: '9px' },
      minWidth: '150px',
      cell: (row: OrderManagementListAttributes) =>
        row.estimateFlg || row.orderFlg || row.invoiceFlg ? null : (
          <div className="text-center">
            <div>{row.serviceStartDate ? formatDate(row.serviceStartDate) : '-'}</div>
            <div>{row.serviceEndDate ? formatDate(row.serviceEndDate) : '-'}</div>
          </div>
        ),
    },
    {
      name: '',
      selector: 'estimateStatus',
      sortable: true,
      style: { alignItems: 'center', paddingTop: '9px', paddingBottom: '9px' },
      minWidth: '240px',
      cell: (row: OrderManagementListAttributes, index: number) =>
        row.invoiceFlg ? null : (
          <Row style={{ alignItems: 'center' }}>
            {row.children && row.children.length > 0 && !row.estimateFlg && row.invoiceFlg === undefined ? (
              <>
                {!row.expandEstimate ? (
                  <i
                    className="icofont icofont-plus pointer pr-1"
                    onClick={() => {
                      initIndex[index] = { ...initIndex[index], index };
                      setPIndex({ ...pIndex, ...initIndex });
                      formikProps.setFieldValue(
                        `dataList[${index}].expandEstimate`,
                        !formikProps.values.dataList[index].expandEstimate
                      );
                    }}
                  />
                ) : (
                  <i
                    className="icofont icofont-minus pointer pr-1"
                    onClick={() => {
                      formikProps.setFieldValue(
                        `dataList[${index}].expandEstimate`,
                        !formikProps.values.dataList[index].expandEstimate
                      );
                      formikProps.setFieldValue(`dataList[${index}].subExpandInvoice`, false);
                      formikProps.setFieldValue(`dataList[${index}].expandInvoice`, false);
                    }}
                  />
                )}
              </>
            ) : (
              <div className="ml-3" />
            )}
            {row.estimateStatus && row.estimateStatus.trim() !== '' ? (
              <div>
                <Link
                  to={{
                    pathname: `${ScreenUrl.ESTIMATE_UPDATE}${row.estimateId}`,
                    state: { from: 's0010', status: row.estimateStatus },
                  }}
                  className="text-link"
                >
                  <div>{row.estimateCode}</div>
                </Link>
                <div style={{ minWidth: '210px', paddingRight: '16px' }}>
                  <Badge
                    className="mr-1"
                    variant={`${
                      // eslint-disable-next-line no-nested-ternary
                      row.estimateStatus === Status.CREATING
                        ? 'primary'
                        : row.estimateStatus === Status.ESTIMATED
                        ? 'success'
                        : 'danger'
                    }`}
                  >
                    {STATUS_MAP[row.estimateStatus]}
                  </Badge>
                  <span style={{ float: 'right' }}>{formatPrice(row.estimateTotalPrice)}</span>
                </div>
              </div>
            ) : (
              <div className="pt-2 pb-2" style={{ textAlign: 'center', minWidth: '180px', padding: '0 16px' }}>
                
              </div>
            )}
          </Row>
        ),
    },
    {
      name: '',
      selector: 'orderStatus',
      sortable: true,
      style: { alignItems: 'center', paddingTop: '9px', paddingBottom: '9px' },
      minWidth: '240px',
      cell: (row: OrderManagementListAttributes) =>
        row.invoiceFlg ? null : (
          <div>
            {row.orderStatus && row.orderStatus.trim() !== '' ? (
              <>
                <Link
                  to={{
                    pathname: `${ScreenUrl.ORDER_UPDATE}${row.orderId}`,
                  }}
                  className="text-link"
                >
                  <div>{row.orderCode}</div>
                </Link>
                <div style={{ minWidth: '210px', paddingRight: '16px' }}>
                  <Badge
                    className="mr-1"
                    variant={`${
                      // eslint-disable-next-line no-nested-ternary
                      row.orderStatus === Status.CREATING
                        ? 'primary'
                        : row.orderStatus === Status.ORDERED
                        ? 'success'
                        : 'danger'
                    }`}
                  >
                    {STATUS_MAP[row.orderStatus]}
                  </Badge>
                  <span style={{ float: 'right' }}>{formatPrice(row.orderTotalPrice)}</span>
                </div>
              </>
            ) : (
              <div className="pt-2 pb-2" style={{ textAlign: 'center', minWidth: '180px', padding: '0 16px' }}>
                
              </div>
            )}
          </div>
        ),
    },
    {
      name: '',
      selector: 'invoiceStatus',
      sortable: true,
      style: { alignItems: 'center', paddingTop: '9px', paddingBottom: '9px' },
      minWidth: '240px',
      cell: (row: OrderManagementListAttributes, index: number) => (
        <Row style={{ alignItems: 'center' }}>
          {(row.children && row.children.length > 0 && row.orderFlg !== undefined) ||
          (row.subChildren && row.subChildren.length > 0 && row.expandEstimate) ? (
            <>
              {(row.expandInvoice && ((row.children && !row.children[0].expandInvoice) || !row.subChildren)) ||
              row.subExpandInvoice ? (
                <i
                  className="icofont icofont-minus pointer pr-1"
                  onClick={() => {
                    const parentIndex = formikProps.values.dataList.findIndex(
                      (value) => value.serviceCode === row.serviceCode
                    );
                    if (
                      parentIndex === index &&
                      formikProps.values.dataList[index].children &&
                      formikProps.values.dataList[index].children?.length === 1
                    ) {
                      if (row.children && row.children?.length > 0)
                        formikProps.setFieldValue(`dataList[${index}].expandInvoice`, false);
                      if (row.subChildren && row.subChildren.length > 0) {
                        formikProps.setFieldValue(`dataList[${pIndex[parentIndex].index}].subExpandInvoice`, false);
                      } else if (row.children && row.children?.length === 1 && row.subChildren?.length === 0) {
                        formikProps.setFieldValue(
                          `dataList[${pIndex[parentIndex].index}].children[${index}].expandInvoice`,
                          false
                        );
                      } else if (index === 0) {
                        formikProps.setFieldValue(`dataList[0].children[0].expandInvoice`, false);
                        formikProps.setFieldValue(`dataList[0].expandInvoice`, false);
                      }
                    } else if (row.subChildren && row.subChildren.length > 0) {
                      formikProps.setFieldValue(`dataList[${index}].subExpandInvoice`, false);
                    } else if (row.children && row.children?.length === 1 && row.subChildren?.length === 0) {
                      formikProps.setFieldValue(
                        `dataList[${pIndex[parentIndex].index}].children[${index}].expandInvoice`,
                        false
                      );
                    } else {
                      formikProps.setFieldValue(
                        `dataList[${pIndex[parentIndex].index}].children[${index}].expandInvoice`,
                        false
                      );
                    }
                  }}
                />
              ) : (
                <i
                  className="icofont icofont-plus pointer pr-1"
                  onClick={() => {
                    const parentIndex = formikProps.values.dataList.findIndex(
                      (value) => value.serviceCode === row.serviceCode
                    );
                    if (
                      parentIndex === index &&
                      formikProps.values.dataList[index].children &&
                      formikProps.values.dataList[index].children?.length === 1
                    ) {
                      if (row.children && row.children?.length > 0 && index !== 0)
                        formikProps.setFieldValue(`dataList[${index}].expandInvoice`, true);
                      if (row.subChildren && row.subChildren.length > 0) {
                        formikProps.setFieldValue(`dataList[${pIndex[parentIndex].index}].subExpandInvoice`, true);
                      } else if (row.children && row.children?.length === 1 && row.subChildren?.length === 0) {
                        formikProps.setFieldValue(
                          `dataList[${pIndex[parentIndex].index}].children[${index}].expandInvoice`,
                          true
                        );
                      } else if (index === 0) {
                        formikProps.setFieldValue(`dataList[0].children[0].expandInvoice`, true);
                        formikProps.setFieldValue(`dataList[0].expandInvoice`, true);
                      }
                    } else if (row.subChildren && row.subChildren.length > 0) {
                      formikProps.setFieldValue(`dataList[${index}].subExpandInvoice`, true);
                    } else if (row.children && row.children?.length === 1 && row.subChildren?.length === 0) {
                      formikProps.setFieldValue(
                        `dataList[${pIndex[parentIndex].index}].children[${index}].expandInvoice`,
                        true
                      );
                    } else {
                      formikProps.setFieldValue(
                        `dataList[${pIndex[parentIndex].index}].children[${index}].expandInvoice`,
                        true
                      );
                    }
                  }}
                />
              )}
            </>
          ) : (
            <div className="ml-3" />
          )}
          {row.invoiceStatus && row.invoiceStatus.trim() !== '' ? (
            <div>
              <Link
                to={{
                  pathname: `${ScreenUrl.INVOICE_UPDATE}${row.invoiceId}`,
                  state: { from: 's0010', status: row.invoiceStatus },
                }}
                className="text-link"
              >
                <div>{row.invoiceCode}</div>
              </Link>
              <div style={{ minWidth: '210px', paddingRight: '16px' }}>
                <Badge
                  className="mr-1"
                  variant={`${
                    // eslint-disable-next-line no-nested-ternary
                    row.invoiceStatus === Status.CREATING
                      ? 'primary'
                      : row.invoiceStatus === Status.INVOICED
                      ? 'success'
                      : 'danger'
                  }`}
                >
                  {STATUS_MAP[row.invoiceStatus]}
                </Badge>
                <span style={{ float: 'right' }}>{formatPrice(row.invoiceTotalPrice)}</span>
              </div>
            </div>
          ) : (
            <div className="pt-2 pb-2" style={{ textAlign: 'center', minWidth: '180px', padding: '0 16px' }}>
              
            </div>
          )}
        </Row>
      ),
    },
    {
      name: '',
      selector: 'projectCode',
      sortable: true,
      style: { alignItems: 'center', paddingTop: '9px', paddingBottom: '9px' },
      minWidth: '180px',
      cell: (row: {
        estimateFlg: boolean;
        orderFlg: boolean;
        invoiceFlg: boolean;
        projectCode: React.ReactNode;
        projectName: React.ReactNode;
      }) =>
        row.estimateFlg || row.orderFlg || row.invoiceFlg ? null : (
          <div>
            <div>{row.projectCode}</div>
            <div>{projectOptions.filter((p) => p.value === row.projectCode)[0]?.label.slice(9)}</div>
          </div>
        ),
    },
  ];

  return (
    <AppLayout>
      <Breadcrumb parent="" title="" />
      <Formik initialValues={initData} onSubmit={(values) => handleSearchSubmit(values)} enableReinitialize>
        {(formikProps) => (
          <Form>
            <Card>
              <CardHeader>
                <h3></h3>
                <Alert isOpen={savedAlert.value}>{savedAlert.message}</Alert>
              </CardHeader>
              <CardBody>
                <Row className="custom-form-group form-inline">
                  <Col className="col-sm-6 col-md-5 col-lg-6 pl-5">
                    <Row style={{ alignItems: 'center' }}>
                      <label className="col-sm-4 pl-0 pr-1" style={{ justifyContent: 'left' }}>
                        
                      </label>
                      <Col className="col-sm-8 pl-0 pr-5">
                        <CustomSelect name="projectCode" options={projectOptions} isClearable />
                      </Col>
                    </Row>
                  </Col>
                  <Col className="col-sm-6 col-md-5 offset-md-2 col-lg-6 offset-lg-0 pl-5">
                    <Row style={{ alignItems: 'center' }}>
                      <label className="col-sm-4 pl-1 pr-0" style={{ justifyContent: 'left' }}>
                        
                      </label>
                      <Col className="col-sm-8 pl-0 pr-5">
                        <CustomSelect name="serviceCode" options={serviceOptions} isClearable />
                      </Col>
                    </Row>
                  </Col>
                </Row>
                <Row className="custom-form-group form-inline">
                  <Col className="col-sm-6 col-md-5 col-lg-6 pl-5">
                    <Row style={{ alignItems: 'center' }}>
                      <label className="col-sm-4 pl-0 pr-1" style={{ justifyContent: 'left' }}>
                        
                      </label>
                      <Col className="col-sm-8 pl-0 pr-5">
                        <CustomSelect name="clientCode" options={clientOptions} isClearable />
                      </Col>
                    </Row>
                  </Col>
                  <Col className="col-sm-6 col-md-5 offset-md-2 col-lg-6 offset-lg-0 pl-5">
                    <Row style={{ alignItems: 'center' }}>
                      <label className="col-sm-4 pl-1 pr-0" style={{ justifyContent: 'left' }}>
                        
                      </label>
                      <div className="col-sm-4 pl-0 pr-3" style={{ maxWidth: '28.4%' }}>
                        <Input className="w-100" type="date" name="serviceStartDate" />
                      </div>
                      <span>~</span>
                      <div className="col-sm-4 pl-3 pr-0" style={{ maxWidth: '28.4%' }}>
                        <Input className="w-100" type="date" name="serviceEndDate" />
                      </div>
                    </Row>
                  </Col>
                </Row>
                <Row className="custom-form-group form-inline">
                  <Col className="search-button ml-0">
                    <Button className="ml-4" variant="info" type="submit">
                       <i className="fa fa-search" />
                    </Button>
                  </Col>
                </Row>
                <Row className="pull-right mr-5">
                  <Button variant="info" type="button" onClick={() => history.push(`${ScreenUrl.SERVICE_CREATE}`)}>
                    
                  </Button>
                </Row>
                <br />
                <label className="switch mb-0">
                  <a
                    href="#"
                    onClick={() => {
                      setProjectMode(!projectMode);
                      formikProps.setFieldValue('projectOpen', {});
                    }}
                  >
                    {!projectMode ? <Folder /> : <List />}
                  </a>
                </label>
                {!projectMode ? (
                  <>
                    <DataTable
                      columns={childColumns(formikProps)}
                      data={formikProps.values.dataList}
                      noHeader
                      loading={loading}
                      expandableRows
                      expandableRowExpanded={(row) => row.subExpandInvoice || row.expandEstimate || row.expandInvoice}
                      expandableRowsHideExpander
                      expandableRowsComponent={
                        <ExpandableComponents
                          data={formikProps.values.dataList}
                          columns={childColumns(formikProps)}
                          loading={loading}
                        />
                      }
                    />
                  </>
                ) : (
                  <>
                    {formikProps.values.projectService.map(
                      (project: { projectCode: string; projectName: string }, index: number) => (
                        <div
                          className="project-tree pb-2 pt-2"
                          key={`data-${
                            formikProps.values.projectService.length + index - formikProps.values.projectService.length
                          }`}
                        >
                          <Row style={{ alignItems: 'center' }}>
                            {formikProps.values.projectOpen[index]?.open === true ? (
                              <Col className="col-md-1 col pl-5 pr-0">
                                <i
                                  className="pointer fa fa-angle-down mr-2"
                                  style={{ fontSize: 'large', paddingRight: '4px' }}
                                  id={`toggler${index}`}
                                  onClick={() => {
                                    projectOpen[index] = { ...projectOpen[index], open: false };
                                    formikProps.setFieldValue('projectOpen', projectOpen);
                                  }}
                                />
                                <i className="fa fa-folder-open-o" style={{ fontSize: 'large' }} />
                              </Col>
                            ) : (
                              <Col className="col-md-1 col pl-5 pr-0">
                                <i
                                  className="pointer fa fa-angle-right mr-2"
                                  style={{ fontSize: 'large', paddingRight: '9px' }}
                                  id={`toggler${index}`}
                                  onClick={() => {
                                    projectOpen[index] = { ...projectOpen[index], open: true };
                                    formikProps.setFieldValue('projectOpen', projectOpen);
                                  }}
                                />
                                <i className="fa fa-folder-o" style={{ fontSize: 'large' }} />
                              </Col>
                            )}

                            <span>
                              {padId(project.projectCode)} {project.projectName}
                            </span>
                          </Row>
                          <UncontrolledCollapse toggler={`#toggler${index}`}>
                            {formikProps.values.projectService[index].Services.map(
                              (service: { serviceId: number; serviceCode: string; serviceName: string }, i: number) => (
                                // eslint-disable-next-line react/no-array-index-key
                                <div
                                  key={`sub-${
                                    formikProps.values.projectService[index].Services.length +
                                    i -
                                    formikProps.values.projectService[index].Services.length
                                  }`}
                                >
                                  <Row>
                                    <Col className="col-md-1 col pl-5 pr-0" />
                                    <i className="pointer fa fa-angle-right pr-3" />
                                    <Link to={`${ScreenUrl.SERVICE_UPDATE}${service.serviceId}`} className="text-link">
                                      {padId(service.serviceCode)} {service.serviceName}
                                    </Link>
                                  </Row>
                                </div>
                              )
                            )}
                          </UncontrolledCollapse>
                        </div>
                      )
                    )}
                  </>
                )}
              </CardBody>
            </Card>
          </Form>
        )}
      </Formik>
    </AppLayout>
  );
};
export default OrderManagement;
