import React from 'react';
import DataTable from '../common/datatable/data-table';

// eslint-disable-next-line react/prop-types,@typescript-eslint/ban-ts-comment
// @ts-ignore
export default ({ data, columns, loading }) =>
  data.children ? (
    <DataTable
      noHeader
      responsive={false}
      columns={columns}
      data={data.children}
      loading={loading}
      pagination={false}
      noTableHead
    />
  ) : null;
