import { Button, Modal, ModalBody, ModalFooter, ModalHeader } from 'reactstrap';
import React, { useEffect, useState } from 'react';

interface FormProps {
  modalProps: { modalTitle: string; message: string; yesTitle: string; noTitle: string; functionOnYes: () => void };
  isOpen: boolean;
  setIsOpen?: React.SetStateAction<any>;
}

const ConfirmModal: React.FC<FormProps> = ({ isOpen, modalProps, setIsOpen }) => {
  const [isOpened, setIsOpened] = useState(false);
  useEffect(() => {
    setIsOpened(isOpen);
  }, []);
  const toggleConfirm = () => {
    setIsOpened(!isOpened);
    setIsOpen(false);
  };

  return (
    <>
      <Modal isOpen={isOpened} toggle={toggleConfirm} className="modal-dialog-centered">
        <ModalHeader>{modalProps.modalTitle}</ModalHeader>
        <ModalBody>{modalProps.message}</ModalBody>
        <ModalFooter className="bookmark pull-right mr-4">
          <Button
            variant="info"
            onClick={() => {
              modalProps.functionOnYes();
              setIsOpened(!isOpened);
            }}
          >
            {modalProps.yesTitle}
          </Button>
          <Button color="light" onClick={toggleConfirm}>
            {modalProps.noTitle}
          </Button>
        </ModalFooter>
      </Modal>
    </>
  );
};

export default ConfirmModal;
