import React, { useEffect, useState } from 'react';
import { Row, Col, Card, CardBody, CardHeader, Button, CardFooter, Alert } from 'reactstrap';
import { useHistory, useLocation, useParams } from 'react-router';
import { FieldArray, Form, Formik, FormikProps } from 'formik';
import { Link } from 'react-router-dom';
import * as Yup from 'yup';
import Breadcrumb from '../components/common/breadcrumb';
import AppLayout from '../components/common/layout';
import DataTable from '../components/common/datatable/data-table';
import { formatDate, formatPrice, padId } from '../components/common/formatter/formatter';
import { CustomSelect, Input } from '../components/common/form-helper';
import DocumentUploader from '../components/common/document-uploader';
import { ScreenUrl, remarksOptions, STATUS_MAP, Status, Remark } from '../constant/commonConstant';
import CustomSidebar from '../components/common/custom-sidebar';
import api from '../utils/api_client';
import CreateEmployee from '../components/employee-process/create-employee';
import {
  DocumentAttributes,
  EstimateCreateAttributes,
  EstimateUpdateAttributes,
  EstimateDocumentAttributes,
} from '../api';
import { columns, OptionsType } from '../components/common/detail-form-common';
import ConfirmModal from '../components/common/confirm-modal';

interface Detail {
  createdAt?: string;
  estimateId?: number;
  estimateDetailId?: number;
  updatedAt?: string;
  person?: string;
  note: string;
  quantity?: number;
  unit?: string;
  unitPrice?: number;
  price?: number;
  remarks: string;
  newFlg?: boolean;
}
interface InitValues {
  estimateId: number;
  estimateDate: string;
  details: Detail[];
  documents: estimateDocument[];
  projectCode: string;
  projectName: string;
  serviceId: number;
  serviceCode: string;
  serviceName: string;
  clientCode: string;
  clientName: string;
  serviceStartDate: string;
  serviceEndDate: string;
  estimateCode: string;
  status: string;
}
const tableData: Detail[] = [];

const blankDetail: Detail = {
  person: '',
  note: '',
  quantity: 0,
  unit: '',
  unitPrice: 0,
  price: 0,
  remarks: '',
  newFlg: false,
};

const newDetail: Detail = {
  person: '',
  note: '',
  quantity: 0,
  unit: '',
  unitPrice: 0,
  price: 0,
  remarks: '',
  newFlg: true,
};

const initialDnDState = {
  draggedFrom: -1,
  draggedTo: -1,
  isDragging: false,
  originalOrder: tableData,
  updatedOrder: tableData,
};

interface linkScreen {
  name: string;
  link: string;
  data: { id: number; code: string; status: string }[];
}

export const blankDocumentInfo: estimateDocument = {
  documentId: 0,
  documentName: '',
  filePath: '',
  documentSaveName: '',
};

export interface estimateDocument extends DocumentAttributes {
  filesInfo?: File;
}

const SchemaValidation = Yup.object().shape({
  estimateCode: Yup.string().nullable().required().length(12),
  estimateDate: Yup.string().nullable().required(),
});

const draftSchemaValidation = Yup.object().shape({
  estimateCode: Yup.string().nullable().required().length(12),
});

const Estimate: React.FC = () => {
  // eslint-disable-next-line @typescript-eslint/ban-ts-comment
  // @ts-ignore
  const { id } = useParams();
  const history = useHistory();
  const location = useLocation();
  const [loading, setLoading] = useState(false);
  const [status, setStatus] = useState(Status.CREATING);
  const [schema, setSchema] = useState(draftSchemaValidation);
  const [editFlag, setEditFlag] = useState(false);
  const [membersOptions, setMembersOptions] = useState<OptionsType[]>([]);
  const [modalMember, setModalMember] = useState(false);
  const [deleteConfirm, setDeleteConfirm] = useState(false);
  const [createConfirm, setCreateConfirm] = useState(false);
  const [orderConfirm, setOrderConfirm] = useState(false);
  const [submit, setSubmit] = useState<boolean | undefined>(undefined);
  const [noScroll, setNoScroll] = useState(false);
  const [savedAlert, setSavedAlert] = useState<{ value: boolean; message: string }>({ value: false, message: '' });
  const [copiedAlert, setCopiedAlert] = useState<{ value: boolean; message: string }>({ value: false, message: '' });
  const [currentIndex, setCurrentIndex] = useState(0);
  const [documents, setDocuments] = useState<estimateDocument[]>([{ ...blankDocumentInfo }]);
  const [prevDocuments, setPrevDocuments] = useState<estimateDocument[]>([blankDocumentInfo]);
  const [preDocId, setPreDocId] = useState<number[]>([]);
  const [delDocId, setDelDocID] = useState<number[]>([]);
  const [disableFlag, setDisableFlag] = useState(false);
  const [copyFlag, setCopyFlag] = useState(false);
  const toggleMember = () => {
    setModalMember(!modalMember);
  };
  const [dragAndDrop, setDragAndDrop] = useState(initialDnDState);
  const classNames = ['table-body row d-flex align-items-stretch'];
  const [deleteDetails, setDeleteDetails] = useState<number[]>([]);
  const [initFindData, setInitFindData] = useState<InitValues>({
    estimateId: 0,
    estimateDate: '',
    details: [{ ...blankDetail }],
    serviceStartDate: '',
    serviceEndDate: '',
    documents: [{ ...blankDocumentInfo }],
    projectCode: '',
    projectName: '',
    serviceId: 0,
    serviceCode: '',
    serviceName: '',
    clientCode: '',
    clientName: '',
    estimateCode: '',
    status: '',
  });
  const [linkData, setLinkData] = useState<linkScreen[]>([]);

  const onDragStart = (event: { currentTarget: { dataset: { position: number } } }, value: Detail[]) => {
    const initialPosition = Number(event.currentTarget.dataset.position);
    setDragAndDrop({
      ...dragAndDrop,
      draggedFrom: initialPosition,
      isDragging: true,
      originalOrder: value,
    });
  };

  const onDragOver = (event: React.DragEvent<HTMLDivElement>, formikProps: FormikProps<InitValues>) => {
    event.preventDefault();

    let newList = dragAndDrop.originalOrder;

    const { draggedFrom } = dragAndDrop;

    const draggedTo = Number(event.currentTarget.dataset.position);

    const itemDragged = newList[draggedFrom];
    const remainingItems = newList.filter((item, index) => index !== draggedFrom);

    newList = [...remainingItems.slice(0, draggedTo), itemDragged, ...remainingItems.slice(draggedTo)];

    if (draggedTo !== dragAndDrop.draggedTo) {
      const updateDetailIdData: Detail[] = [];
      newList.forEach((n, i) => {
        updateDetailIdData.push({ ...newList[i], estimateDetailId: formikProps.values.details[i].estimateDetailId });
      });
      setDragAndDrop({
        ...dragAndDrop,
        updatedOrder: updateDetailIdData,
        draggedTo,
      });
    }
  };

  const onDrop = (formikProps: FormikProps<InitValues>) => {
    formikProps.setFieldValue('details', [...dragAndDrop.updatedOrder]);
    setDragAndDrop({
      ...dragAndDrop,
      draggedFrom: -1,
      draggedTo: -1,
      isDragging: false,
    });
  };

  const onDragLeave = () => {
    setDragAndDrop({
      ...dragAndDrop,
      draggedTo: 0,
    });
  };

  const handleSubmit = (values: InitValues) => {
    const submitDetails: React.SetStateAction<any[]> = [];
    const prevArrDocId: number[] = prevDocuments
      ? prevDocuments.map((doc: estimateDocument) => doc.documentId).filter(Boolean)
      : [];
    const arrDocId: number[] = values.documents
      ? values.documents.map((doc: estimateDocument) => doc.documentId).filter(Boolean)
      : [];
    const createDocId: number[] = [];
    arrDocId.forEach((val) => {
      if (prevArrDocId.indexOf(val) <= -1) createDocId.push(val);
    });
    const estimateDocCreateValue: EstimateDocumentAttributes = {
      estimateId: values.estimateId,
      documents: !copyFlag ? createDocId : arrDocId,
    };
    const setInitMsg = () => {
      setTimeout(() => {
        setSavedAlert({ value: false, message: '' });
        setCreateConfirm(false);
        setSubmit(false);
      }, 3000);
    };
    const setMessage = (statusValue: string) => {
      if (statusValue === Status.ESTIMATED) {
        setSavedAlert({ value: true, message: '' });
        setInitMsg();
      } else if (statusValue === Status.CREATING || statusValue === Status.ESTIMATE_CHANGED) {
        setSavedAlert({ value: true, message: '' });
        setInitMsg();
      }
      setStatus(statusValue);
    };

    if (!id) {
      if (values.status === Status.ESTIMATED) {
        setCreateConfirm(true);
      }
      values.details.forEach((item, i: number) =>
        submitDetails.push({
          index: i + 1,
          // estimateId: values.estimateId,
          note: item.person ? item.person : item.note,
          person: item.person ? item.person : null,
          quantity: item.quantity ? item.quantity : null,
          unit: item.unit ? item.unit : null,
          unitPrice: item.unitPrice ? item.unitPrice : null,
          price:
            // eslint-disable-next-line no-nested-ternary
            item.quantity && item.unitPrice
              ? item.remarks === Remark.DISCOUNT
                ? -Math.abs(item.quantity * item.unitPrice)
                : item.quantity * item.unitPrice
              : null,
          remarks: item.remarks,
        })
      );
      const createValue: EstimateCreateAttributes = {
        serviceId: values.serviceId,
        estimateCode: values.estimateCode.trim(),
        status: values.status,
        estimateDate: values.estimateDate,
        details: submitDetails,
      };
      if (submit) {
        api.estimatePost(createValue).then((estimate) => {
          const { estimateId } = estimate.data as any;
          delDocId.forEach((delId) => {
            api.deleteFile(delId);
          });
          if (estimateDocCreateValue.documents.length > 0) {
            api.createEstimateDocument({ ...estimateDocCreateValue, estimateId }).then(() => {
              setMessage(String(createValue.status));
            });
          } else {
            setMessage(String(createValue.status));
          }
          setCopiedAlert({ value: false, message: '' });
          history.push({
            pathname: `${ScreenUrl.ESTIMATE_UPDATE}${estimateId}`,
          });
        });
      }
    } else {
      if (values.status === Status.ESTIMATED) {
        setCreateConfirm(true);
      }
      values.details.forEach((item, i: number) =>
        submitDetails.push({
          estimateDetailId: item.estimateDetailId ? item.estimateDetailId : null,
          index: i + 1,
          estimateId: values.estimateId,
          note: item.person ? item.person : item.note,
          person: item.person ? item.person : null,
          quantity: item.quantity ? item.quantity : null,
          unit: item.unit ? item.unit : null,
          unitPrice: item.unitPrice ? item.unitPrice : null,
          price:
            // eslint-disable-next-line no-nested-ternary
            item.quantity && item.unitPrice
              ? item.remarks === Remark.DISCOUNT
                ? -Math.abs(item.quantity * item.unitPrice)
                : item.quantity * item.unitPrice
              : null,
          remarks: item.remarks,
        })
      );
      const updateValue: EstimateUpdateAttributes = {
        serviceId: values.serviceId,
        estimateCode: values.estimateCode.trim(),
        status: values.status,
        estimateDate: formatDate(values.estimateDate, 'yyyy-MM-dd'),
        details: submitDetails,
        orderFlg: disableFlag,
        deleteFlg: false,
        deleteDetails,
      };
      if (submit) {
        api.estimateUpdate(values.estimateId, updateValue).then(() => {
          if (delDocId.length > 0) {
            delDocId.forEach((delId) => {
              api.deleteEstimateDocument(delId, values.estimateId).then(() => {
                if (estimateDocCreateValue.documents.length > 0) {
                  api
                    .createEstimateDocument({ ...estimateDocCreateValue, estimateId: values.estimateId })
                    .then(() => setMessage(String(updateValue.status)));
                } else {
                  setMessage(String(updateValue.status));
                }
              });
            });
          } else if (estimateDocCreateValue.documents.length > 0) {
            api
              .createEstimateDocument({ ...estimateDocCreateValue, estimateId: values.estimateId })
              .then(() => setMessage(String(updateValue.status)));
          } else {
            setMessage(String(updateValue.status));
          }
        });
      }
      history.push(location.pathname);
    }
  };

  const [detailFlg, setDetailFlg] = useState(false);

  const fetchData = async () => {
    api.employeeGetAll().then((res) => {
      const memberList = res.data.map((member: { employeeCode: string; employeeName: string }) => ({
        value: member.employeeCode,
        label: member.employeeName,
      }));
      setMembersOptions(memberList);
    });
    if ((location.state !== undefined && location.state !== '' && typeof location.state === 'object') || id) {
      setEditFlag(true);
    }

    if (id) {
      api.getEstimateById(id).then((res) => {
        const data = res.data as any;
        setDisableFlag(data.orderFlg === true);
        if (data.linkData) {
          const invoices: { id: number; code: string; status: string }[] = [];
          if (data.linkData.Invoices.length > 0) {
            data.linkData.Invoices.forEach((i: { invoiceId: number; invoiceCode: string; invoiceStatus: string }) => {
              if (i.invoiceStatus.trim() !== '')
                invoices.push({ id: i.invoiceId, code: i.invoiceCode, status: i.invoiceStatus });
            });
          }
          setLinkData([
            {
              name: '',
              link: `${ScreenUrl.ORDER_UPDATE}`,
              data:
                data.linkData.orderStatus.trim() !== ''
                  ? [{ id: data.linkData.orderId, code: data.linkData.orderCode, status: data.linkData.orderStatus }]
                  : [],
            },
            {
              name: '',
              link: `${ScreenUrl.INVOICE_UPDATE}`,
              data: invoices,
            },
          ]);
        }
        setInitFindData({
          ...initFindData,
          ...data,
          estimateDate: formatDate(data.estimateDate, 'yyyy-MM-dd'),
          details: data.details,
        });
        setStatus(data.status);
      });
      const documentRes = await api.getEstimateDocuments(id);
      const documentData = [...documentRes.data, blankDocumentInfo];
      setDocuments(documentData);
      setPrevDocuments(documentData);
      const prevArrDocId: number[] = documentData
        ? documentData.map((doc: estimateDocument) => doc.documentId).filter(Boolean)
        : [];
      setPreDocId(prevArrDocId);
    } else if (typeof location.state === 'number') {
      setDetailFlg(true);
      const serviceId = location.state;
      api.getServiceById(serviceId).then((res) => {
        const service = res.data as any;
        setInitFindData({
          ...initFindData,
          ...service,
          status: Status.CREATING,
          estimateDate: '',
          estimateCode: padId(service.serviceCode.trim(), 12),
        });
      });
    } else if (typeof location.state === 'object') {
      setCopyFlag(true);
      setCopiedAlert({ value: true, message: '' });
      setTimeout(() => {
        setCopiedAlert({ value: false, message: '' });
      }, 3000);
      const copyValue = location.state as InitValues;
      const oldEstimateId = (location.state as { estimateId: number }).estimateId;
      const data: InitValues = {
        ...copyValue,
        status: Status.CREATING,
        estimateCode: padId(Number(copyValue.estimateCode) + 1, 12),
        estimateDate: formatDate(copyValue.estimateDate, 'yyyy-MM-dd'),
      };

      setEditFlag(false);
      setDetailFlg(true);
      setDisableFlag(false);
      setStatus(Status.CREATING);
      api.getEstimateDocuments(oldEstimateId).then((docs) => {
        setDocuments([...docs.data, blankDocumentInfo]);
      });

      setInitFindData({
        ...initFindData,
        ...data,
        estimateCode: data.estimateCode,
        estimateDate: formatDate(data.estimateDate, 'yyyy-MM-dd'),
        details: data.details,
      });
    }
    setLoading(false);
  };

  useEffect(() => {
    setLoading(true);
    fetchData().then();
  }, [id, location.state, savedAlert.value]);
  const initData =
    id || documents.length > 1
      ? { ...initFindData, documents }
      : { ...initFindData, documents: [{ ...blankDocumentInfo }] };

  const callDeleteEstimate = (act: boolean, redirect: string) => {
    if (act) {
      api.deleteEstimate(id).then(() => {
        history.push(redirect, true);
      });
    }
  };

  return (
    <AppLayout>
      <Formik
        initialValues={initData}
        onSubmit={(values) => handleSubmit(values)}
        validationSchema={schema}
        validate={(values) => {
          if (values.status !== Status.CREATING) {
            setSchema(SchemaValidation);
          } else setSchema(draftSchemaValidation);
        }}
        enableReinitialize
      >
        {(formikProps) => (
          <Form>
            {!noScroll && Object.keys(formikProps.errors).length > 0 && window.scrollTo(0, 0)}
            {(linkData[0]?.data.length > 0 || linkData[1]?.data.length > 0) && (
              <CustomSidebar
                element={
                  <>
                    {linkData.map((item, index) => (
                      <div
                        key={`linkData-${linkData.length + index - linkData.length}`}
                        className="mb-4"
                        style={{ borderBottom: '1px solid whitesmoke' }}
                      >
                        {linkData[index].data.length > 0 && (
                          <>
                            <h6>{item.name}</h6>
                            {linkData[index].data.map((rowData, i) => (
                              <div key={`details-${linkData[index].data.length + i - linkData[index].data.length}`}>
                                <div className="form-inline">
                                  <label className="col-sm-3 pl-0 pr-0">{item.name}:</label>
                                  <Link
                                    to={{ pathname: `${item.link}${rowData.id}`, state: 's0012' }}
                                    className="col-sm-5 pl-0 pr-0"
                                  >
                                    {rowData.code ? padId(rowData.code, 12) : ''}
                                  </Link>
                                  <div className="col-sm-4 pl-0 pr-0">
                                    {rowData.status !== '' ? STATUS_MAP[rowData.status] : ''}
                                  </div>
                                </div>
                              </div>
                            ))}
                          </>
                        )}
                      </div>
                    ))}
                  </>
                }
              />
            )}
            <Breadcrumb
              parent=""
              title=""
              bookmarkComponent={
                id && (
                  <div className="bookmark pull-right mr-4">
                    <Row>
                      <Button
                        variant="info"
                        className="mr-3"
                        onClick={() => {
                          history.push({
                            pathname: ScreenUrl.ESTIMATE_CREATE,
                            state: formikProps.values,
                          });
                        }}
                        disabled={disableFlag}
                      >
                        <i className="icofont icofont-copy-alt" />
                      </Button>
                      <Button
                        variant="info"
                        className="mr-1"
                        onClick={() => {
                          setDeleteConfirm(!deleteConfirm);
                        }}
                        disabled={disableFlag}
                      >
                        <i className="icofont icofont-trash" />
                      </Button>
                    </Row>
                  </div>
                )
              }
            />
            <Card>
              <CardHeader>
                <h3></h3>
                {savedAlert.value && window.scrollTo(0, 0)}
                <Alert isOpen={savedAlert.value}>{savedAlert.message}</Alert>
                <Alert isOpen={copiedAlert.value}>{copiedAlert.message}</Alert>
                {deleteConfirm && (
                  <ConfirmModal
                    isOpen={deleteConfirm}
                    modalProps={{
                      modalTitle: '',
                      message: '?',
                      yesTitle: '',
                      noTitle: '',
                      functionOnYes: () => callDeleteEstimate(true, `${ScreenUrl.ORDER_MANAGEMENT}`),
                    }}
                  />
                )}
                {createConfirm && (
                  <ConfirmModal
                    isOpen={createConfirm}
                    setIsOpen={setCreateConfirm}
                    modalProps={{
                      modalTitle: '',
                      message: '?',
                      yesTitle: '',
                      noTitle: '',
                      functionOnYes: () => {
                        setSubmit(true);
                        formikProps.submitForm().then(() => window.scrollTo(0, 0));
                      },
                    }}
                  />
                )}
                {orderConfirm && (
                  <ConfirmModal
                    isOpen={orderConfirm}
                    setIsOpen={setOrderConfirm}
                    modalProps={{
                      modalTitle: '',
                      message: '',
                      yesTitle: '',
                      noTitle: '',
                      functionOnYes: () => {
                        history.push({
                          pathname: `${ScreenUrl.ORDER_CREATE}`,
                          state: { id: formikProps.values.estimateId, from: 's0012', savedTemp: true },
                        });
                      },
                    }}
                  />
                )}
              </CardHeader>
              <CardBody className="tabbed-card">
                {modalMember && (
                  <CreateEmployee
                    parentFormikProps={formikProps}
                    currentIndex={currentIndex}
                    dataProps={formikProps.values.details[currentIndex]?.person}
                    isOpen={modalMember}
                    memberOptions={membersOptions}
                  />
                )}
                <Card className="b-r-0" style={{ boxShadow: 'none' }}>
                  <Row className="custom-form-group form-inline">
                    <Col className="col-sm-6 col-md-5 col-lg-6 pl-5">
                      <Row style={{ alignItems: 'center' }}>
                        <label className="col-sm-3 pl-0 pr-1" style={{ justifyContent: 'left' }}>
                          
                        </label>
                        <Col className="col-sm-8 pl-0">
                          <span>
                            {formikProps.values.projectCode} {formikProps.values.projectName}
                          </span>
                        </Col>
                      </Row>
                    </Col>
                    <Col className="col-sm-6 col-md-5 offset-md-2 col-lg-6 offset-lg-0 pl-5">
                      <Row style={{ alignItems: 'center' }}>
                        <label className="col-sm-3 pl-1 pr-0" style={{ justifyContent: 'left' }}>
                          
                        </label>
                        <Col className="col-sm-8 pl-0">
                          <span className="col-sm-4 pl-0 pr-4">{formatDate(formikProps.values.serviceStartDate)}</span>
                          {formikProps.values.serviceStartDate || formikProps.values.serviceEndDate ? (
                            <span>~</span>
                          ) : null}
                          <span className="col-sm-4 pr-0 pl-4">{formatDate(formikProps.values.serviceEndDate)}</span>
                        </Col>
                      </Row>
                    </Col>
                  </Row>
                  <Row className="custom-form-group form-inline">
                    <Col className="col-sm-6 col-md-5 col-lg-6 pl-5">
                      <Row style={{ alignItems: 'center' }}>
                        <label className="col-sm-3 pl-0 pr-1" style={{ justifyContent: 'left' }}>
                          
                        </label>
                        <Col className="col-sm-8 pl-0">
                          <span>
                            {formikProps.values.serviceCode} {formikProps.values.serviceName}
                          </span>
                        </Col>
                      </Row>
                    </Col>
                  </Row>
                  <Row className="custom-form-group form-inline">
                    <Col className="col-sm-6 col-md-5 col-lg-6 pl-5">
                      <Row style={{ alignItems: 'center' }}>
                        <label className="col-sm-3 pl-0 pr-1" style={{ justifyContent: 'left' }}>
                          
                        </label>
                        <Col className="col-sm-8 pl-0">
                          <span>{formikProps.values.clientName}</span>
                        </Col>
                      </Row>
                    </Col>
                    <Col className="col-sm-6 col-md-5 offset-md-2 col-lg-6 offset-lg-0 pl-5">
                      <Row style={{ alignItems: 'center' }}>
                        <label className="col-sm-3 pl-1 pr-0" style={{ justifyContent: 'left' }}>
                          
                        </label>
                        <div className="form-inline col-sm-8 pl-0">
                          <span>{STATUS_MAP[formikProps.values.status]}</span>
                        </div>
                      </Row>
                    </Col>
                  </Row>
                  <Row className="custom-form-group form-inline">
                    <Col className="col-sm-6 col-md-5 col-lg-6 pl-5">
                      <Row style={{ alignItems: 'center' }}>
                        <label className="col-sm-3 pl-0 pr-1 required" style={{ justifyContent: 'left' }}>
                          
                        </label>
                        {disableFlag ? (
                          <Col className="col-sm-8 pl-0">
                            <span>{formikProps.values.estimateCode}</span>
                          </Col>
                        ) : (
                          <Col className="col-sm-8 pl-0">
                            <Input className="form-control" type="text" name="estimateCode" style={{ width: '100%' }} />
                          </Col>
                        )}
                      </Row>
                    </Col>
                    <Col className="col-sm-6 col-md-5 offset-md-2 col-lg-6 offset-lg-0 pl-5">
                      <Row style={{ alignItems: 'center' }}>
                        <label className="col-sm-3 pl-0 pr-1 required" style={{ justifyContent: 'left' }}>
                          
                        </label>
                        {disableFlag ? (
                          <Col className="col-sm-8 pl-0">
                            <span>{formatDate(formikProps.values.estimateDate)}</span>
                          </Col>
                        ) : (
                          <Col className="col-sm-8 pl-0">
                            <Input className="form-control w-50" type="date" name="estimateDate" />
                          </Col>
                        )}
                      </Row>
                    </Col>
                  </Row>
                </Card>
                <Card className="b-r-0" style={{ boxShadow: 'none' }}>
                  <CardHeader
                    style={{ padding: '7px', backgroundColor: '#ddd', borderTopLeftRadius: 0, borderTopRightRadius: 0 }}
                  >
                    <h6 style={{ marginBottom: '0px' }}></h6>
                  </CardHeader>
                  <CardBody>
                    <div className="custom-form-group form-inline" style={{ justifyContent: 'flex-end' }}>
                      {id && (
                        <Button
                          variant="info"
                          type="button"
                          disabled={disableFlag}
                          onClick={() => {
                            setOrderConfirm(!orderConfirm);
                          }}
                        >
                          
                        </Button>
                      )}
                    </div>
                    <div>
                      {detailFlg && !disableFlag ? (
                        <FieldArray name="details">
                          {(arrayHelper) => (
                            <div style={{ width: '950px', paddingRight: '20px' }}>
                              <div className="table table-striped">
                                <div
                                  className="row font-weight-bold pt-3 pb-3"
                                  style={{ paddingLeft: '5px', paddingRight: '5px' }}
                                >
                                  <div style={{ width: '3%' }} />
                                  <div style={{ width: '22%' }} className="ml-1 ">
                                    
                                  </div>
                                  <div style={{ width: '7%' }} className="ml-1 ">
                                    
                                  </div>
                                  <div style={{ width: '8%' }} className="ml-1 ">
                                    
                                  </div>
                                  <div style={{ width: '12%' }} className="ml-1 ">
                                    
                                  </div>
                                  <div style={{ width: '18%' }} className="ml-1 ">
                                    
                                  </div>
                                  <div style={{ width: '20%' }} className="ml-1 ">
                                    
                                  </div>
                                  <div style={{ width: '6%', padding: '0 9px' }}>
                                    <i
                                      className="icofont icofont-ui-add pr-2"
                                      onClick={() => {
                                        setNoScroll(true);
                                        arrayHelper.insert(0, editFlag ? { ...newDetail } : { ...blankDetail });
                                      }}
                                      hidden={disableFlag}
                                    />
                                  </div>
                                </div>
                                {formikProps.values.details?.map((item, index: number) => (
                                  <div
                                    key={`detail-${
                                      formikProps.values.details.length + index - formikProps.values.details.length
                                    }`}
                                    id={String(index)}
                                    data-position={index}
                                    draggable
                                    // eslint-disable-next-line @typescript-eslint/ban-ts-comment
                                    // @ts-ignore
                                    onDragStart={(e) => onDragStart(e, formikProps.values.details)}
                                    onDragOver={(e) => onDragOver(e, formikProps)}
                                    onDrop={() => onDrop(formikProps)}
                                    onDragLeave={onDragLeave}
                                    className={
                                      dragAndDrop && dragAndDrop.draggedTo === Number(index)
                                        ? classNames.join(' dropArea')
                                        : classNames.join('')
                                    }
                                  >
                                    <div style={{ width: '3%', paddingTop: '9px' }} className="text-right">
                                      {index + 1}
                                    </div>
                                    <div style={{ width: '22%' }} className="ml-1 form-inline">
                                      {item.remarks === Remark.MEMBER ? (
                                        <>
                                          <div style={{ width: '90%' }}>
                                            <CustomSelect
                                              name={`details[${index}].person`}
                                              onChange={(value) =>
                                                formikProps.setFieldValue(
                                                  `details[${index}].person`,
                                                  membersOptions.filter((member) => member.value === value)[0].value
                                                )
                                              }
                                              options={membersOptions}
                                              isDisabled={editFlag && item.newFlg === undefined}
                                            />
                                          </div>
                                          <i
                                            style={{ width: '10%' }}
                                            className="pointer icofont icofont-ui-add pl-1"
                                            onClick={() => {
                                              setCurrentIndex(index);
                                              formikProps.setFieldValue(
                                                `details[${index}].person`,
                                                membersOptions.slice(-1)[0]?.value
                                                  ? padId(Number(membersOptions.slice(-1)[0].value) + 1, 12)
                                                  : padId(1, 12)
                                              );
                                              toggleMember();
                                            }}
                                            hidden={editFlag && item.newFlg === undefined}
                                          />
                                        </>
                                      ) : (
                                        <Input
                                          name={`details[${index}].note`}
                                          style={{ width: '90%' }}
                                          disabled={
                                            editFlag && item.remarks !== Remark.TEXT && item.newFlg === undefined
                                          }
                                        />
                                      )}
                                    </div>
                                    <div style={{ width: '7%' }} className="ml-1">
                                      <Input
                                        type="number"
                                        min={0}
                                        name={`details[${index}].quantity`}
                                        className="text-right"
                                        onValueChange={(value) =>
                                          item.remarks === Remark.DISCOUNT
                                            ? formikProps.setFieldValue(
                                                `details[${index}].price`,
                                                -Math.abs(item.unitPrice ? Number(value) * item.unitPrice : 0)
                                              )
                                            : formikProps.setFieldValue(
                                                `details[${index}].price`,
                                                item.unitPrice ? Number(value) * item.unitPrice : 0
                                              )
                                        }
                                        disabled={
                                          (editFlag && item.newFlg === undefined) || item.remarks === Remark.TEXT
                                        }
                                      />
                                    </div>
                                    <div style={{ width: '8%' }} className="ml-1">
                                      <Input
                                        name={`details[${index}].unit`}
                                        disabled={
                                          item.remarks === Remark.TEXT || (editFlag && item.newFlg === undefined)
                                        }
                                      />
                                    </div>
                                    <div style={{ width: '12%' }} className="ml-1">
                                      <Input
                                        type="number"
                                        min={0}
                                        name={`details[${index}].unitPrice`}
                                        className="text-right"
                                        onValueChange={(value) =>
                                          item.remarks === Remark.DISCOUNT
                                            ? formikProps.setFieldValue(
                                                `details[${index}].price`,
                                                -Math.abs(item.quantity ? item.quantity * Number(value) : 0)
                                              )
                                            : formikProps.setFieldValue(
                                                `details[${index}].price`,
                                                item.quantity ? item.quantity * Number(value) : 0
                                              )
                                        }
                                        disabled={
                                          item.remarks === Remark.TEXT || (editFlag && item.newFlg === undefined)
                                        }
                                      />
                                    </div>
                                    <div
                                      style={{
                                        width: '18%',
                                        color: item.remarks === Remark.DISCOUNT ? 'red' : undefined,
                                        backgroundColor:
                                          item.remarks === Remark.TEXT || (editFlag && item.newFlg === undefined)
                                            ? '#e9ecef'
                                            : undefined,
                                        opacity: item.remarks === Remark.TEXT ? 1 : undefined,
                                      }}
                                      className="form-control text-right ml-1"
                                    >
                                      {item.remarks === Remark.DISCOUNT && ' '}
                                      {item.quantity && item.unitPrice
                                        ? formatPrice(item.quantity * item.unitPrice)
                                        : ''}
                                    </div>
                                    <div style={{ width: '20%' }} className="ml-1">
                                      <CustomSelect
                                        name={`details[${index}].remarks`}
                                        options={remarksOptions}
                                        onChange={(value) => {
                                          formikProps.setFieldValue(
                                            `details[${index}].price`,
                                            item.quantity && item.unitPrice ? item.quantity * item.unitPrice : 0
                                          );
                                          if (value === Remark.TEXT) {
                                            formikProps.setFieldValue(`details[${index}].quantity`, 0);
                                            formikProps.setFieldValue(`details[${index}].unit`, '');
                                            formikProps.setFieldValue(`details[${index}].unitPrice`, 0);
                                          } else if (value === Remark.DISCOUNT) {
                                            formikProps.setFieldValue(
                                              `details[${index}].price`,
                                              -Math.abs(
                                                item.quantity && item.unitPrice ? item.quantity * item.unitPrice : 0
                                              )
                                            );
                                          }
                                        }}
                                        isDisabled={editFlag && item.newFlg === undefined}
                                      />
                                    </div>
                                    <div style={{ width: '6%', paddingTop: '9px' }}>
                                      <div className="form-inline pointer pl-2">
                                        <i
                                          className="icofont icofont-ui-add pr-2"
                                          onClick={() => {
                                            setNoScroll(true);
                                            arrayHelper.insert(
                                              index + 1,
                                              editFlag ? { ...newDetail } : { ...blankDetail }
                                            );
                                          }}
                                          hidden={editFlag && item.newFlg === undefined && item.remarks !== Remark.TEXT}
                                        />
                                        {formikProps.values.details.length === 1 && index === 0 ? (
                                          <i
                                            className="icofont icofont-trash"
                                            onClick={(e) => {
                                              e.preventDefault();
                                              arrayHelper.remove(index);
                                              if (item.estimateDetailId) {
                                                setDeleteDetails([...deleteDetails, item.estimateDetailId]);
                                              }
                                              setNoScroll(true);
                                              arrayHelper.insert(index + 1, { ...blankDetail });
                                            }}
                                            hidden={
                                              editFlag && item.newFlg === undefined && item.remarks !== Remark.TEXT
                                            }
                                          />
                                        ) : (
                                          <i
                                            className="icofont icofont-trash"
                                            onClick={(e) => {
                                              e.preventDefault();
                                              arrayHelper.remove(index);
                                              if (item.estimateDetailId) {
                                                setDeleteDetails([...deleteDetails, item.estimateDetailId]);
                                              }
                                            }}
                                            hidden={
                                              editFlag && item.newFlg === undefined && item.remarks !== Remark.TEXT
                                            }
                                          />
                                        )}
                                      </div>
                                    </div>
                                  </div>
                                ))}
                              </div>
                            </div>
                          )}
                        </FieldArray>
                      ) : (
                        <DataTable
                          style={{ width: '950px', paddingRight: '20px' }}
                          onRowClicked={() => setDetailFlg(true)}
                          columns={columns(membersOptions)}
                          data={formikProps.values.details}
                          pagination={false}
                          noHeader
                          subHeaderAlign="left"
                          loading={loading}
                          persistTableHead
                        />
                      )}
                    </div>
                  </CardBody>
                  <CardFooter>
                    {detailFlg && !disableFlag && (
                      <Row>
                        <Button
                          variant="info"
                          type="button"
                          onClick={() => {
                            setDetailFlg(!detailFlg);
                            setLoading(false);
                            setNoScroll(true);
                          }}
                          disabled={disableFlag}
                        >
                          
                        </Button>
                      </Row>
                    )}
                    <Row>
                      <Col className="form-group form-inline col-lg-group" />
                      <Col className="form-group form-inline col-lg-group">
                        <Col className="col-sm-5" />
                        <Col className="col-sm-3">
                          <span>:</span>
                        </Col>
                        <Col
                          className="col-sm-4"
                          style={{
                            textAlign: 'right',
                            color:
                              formikProps.values.details?.reduce(
                                (sum: number, item) => (item.price ? sum + item.price : sum),
                                0
                              ) < 0
                                ? 'red'
                                : undefined,
                          }}
                        >
                          {formikProps.values.details?.reduce(
                            (sum: number, item) => (item.price ? sum + item.price : sum),
                            0
                          ) < 0 && ' '}
                          {formatPrice(
                            Math.abs(
                              formikProps.values.details?.reduce(
                                (sum: number, item) => (item.price ? sum + item.price : sum),
                                0
                              )
                            )
                          )}
                        </Col>
                      </Col>
                    </Row>
                    <Row>
                      <Col className="form-group form-inline col-lg-group" />
                      <Col className="form-group form-inline col-lg-group">
                        <Col className="col-sm-5" />
                        <Col className="col-sm-3">
                          <span>:</span>
                        </Col>
                        <Col
                          className="col-sm-4"
                          style={{
                            textAlign: 'right',
                            color:
                              formikProps.values.details?.reduce(
                                (sum: number, item) => (item.price ? sum + item.price : sum),
                                0
                              ) *
                                0.1 <
                              0
                                ? 'red'
                                : undefined,
                          }}
                        >
                          {formikProps.values.details?.reduce(
                            (sum: number, item) => (item.price ? sum + item.price : sum),
                            0
                          ) *
                            0.1 <
                            0 && ' '}
                          {formatPrice(
                            Math.abs(
                              formikProps.values.details?.reduce(
                                (sum: number, item) => (item.price ? sum + item.price : sum),
                                0
                              ) * 0.1
                            )
                          )}
                        </Col>
                      </Col>
                    </Row>
                    <Row>
                      <Col className="form-group form-inline col-lg-group" />
                      <Col className="form-group form-inline col-lg-group">
                        <Col className="col-sm-5" />
                        <Col className="col-sm-3">
                          <span>:</span>
                        </Col>
                        <Col
                          className="col-sm-4"
                          style={{
                            textAlign: 'right',
                            color:
                              formikProps.values.details?.reduce(
                                (sum: number, item) => (item.price ? sum + item.price : sum),
                                0
                              ) +
                                formikProps.values.details?.reduce(
                                  (sum: number, item) => (item.price ? sum + item.price : sum),
                                  0
                                ) *
                                  0.1 <
                              0
                                ? 'red'
                                : undefined,
                          }}
                        >
                          {formikProps.values.details?.reduce(
                            (sum: number, item) => (item.price ? sum + item.price : sum),
                            0
                          ) +
                            formikProps.values.details?.reduce(
                              (sum: number, item) => (item.price ? sum + item.price : sum),
                              0
                            ) *
                              0.1 <
                            0 && ' '}
                          {formatPrice(
                            Math.abs(
                              formikProps.values.details?.reduce(
                                (sum: number, item) => (item.price ? sum + item.price : sum),
                                0
                              ) +
                                formikProps.values.details?.reduce(
                                  (sum: number, item) => (item.price ? sum + item.price : sum),
                                  0
                                ) *
                                  0.1
                            )
                          )}
                        </Col>
                      </Col>
                    </Row>
                  </CardFooter>
                </Card>
                <Card className="b-r-0" style={{ boxShadow: 'none' }}>
                  <CardHeader
                    style={{ padding: '7px', backgroundColor: '#ddd', borderTopLeftRadius: 0, borderTopRightRadius: 0 }}
                  >
                    <h6 style={{ marginBottom: '0px' }}></h6>
                  </CardHeader>
                  <CardBody>
                    <FieldArray name="documents">
                      {(arrayHelper) => (
                        <>
                          {formikProps.values.documents.length > 0
                            ? formikProps.values.documents?.map((document, index) => (
                                <Row key={`serviceDocument-${index + 1}`}>
                                  <Col className="form-group ">
                                    <DocumentUploader
                                      name={`documents[${index}].documentId`}
                                      documentName={
                                        document.documentSaveName ? document.documentSaveName : document.documentName
                                      }
                                      documentId={document.documentId}
                                      filePath={document.filePath}
                                      filesInfo={document.filesInfo}
                                      deleteDoc={!disableFlag}
                                      disableAdd={disableFlag}
                                      onSelect={(files: File) => {
                                        arrayHelper.replace(index, {
                                          ...document,
                                          filesInfo: files,
                                          documentName: files.name,
                                        });
                                      }}
                                      onUpload={(resDocId: number, resFilePath: string, resDocumentSaveName) => {
                                        arrayHelper.replace(index, {
                                          ...document,
                                          documentId: resDocId,
                                          filePath: resFilePath,
                                          documentName: resDocumentSaveName || document.documentName,
                                        });
                                        setNoScroll(true);
                                        arrayHelper.insert(index + 1, blankDocumentInfo);
                                      }}
                                      onDelete={(delId) => {
                                        arrayHelper.remove(index);
                                        if (delId) {
                                          delDocId.push(delId);
                                        }
                                      }}
                                      update={!!id}
                                      prevDoc={preDocId}
                                    />
                                  </Col>
                                </Row>
                              ))
                            : ''}
                        </>
                      )}
                    </FieldArray>
                  </CardBody>
                  <CardFooter />
                </Card>
                <div className="custom-form-group form-inline" style={{ justifyContent: 'flex-end' }}>
                  <Button
                    variant="info"
                    type="submit"
                    className="mr-3"
                    onClick={() => {
                      setNoScroll(false);
                      if (status === Status.ESTIMATED || status === Status.ESTIMATE_CHANGED) {
                        formikProps.setFieldValue('status', Status.ESTIMATE_CHANGED);
                      } else {
                        formikProps.setFieldValue('status', Status.CREATING);
                      }
                      setSubmit(true);
                    }}
                    disabled={disableFlag}
                  >
                    
                  </Button>
                  <Button
                    variant="info"
                    type="submit"
                    onClick={() => {
                      setNoScroll(false);
                      formikProps.setFieldValue('status', Status.ESTIMATED);
                    }}
                    disabled={disableFlag}
                  >
                    
                  </Button>
                </div>
              </CardBody>
            </Card>
          </Form>
        )}
      </Formik>
    </AppLayout>
  );
};
export default Estimate;
