import React, { FC, useState } from 'react';
import { Link } from 'react-router-dom';
import Select, { OptionsType } from 'react-select';
import { Button, Col, Row } from 'reactstrap';
import { Form, Formik } from 'formik';
import { Input, OptionType } from './form-helper';
import api from '../../utils/api_client';

interface DocumentUploaderProps {
  upload?: boolean;
  index?: number;
  name: string;
  deleteDoc?: boolean;
  onUpload?: (documentID: number, filePath: string, documentSaveName: string) => void;
  documentId?: number;
  filesInfo?: File;
  documentType?: string;
  onDelete?: (id?: number) => void;
  onSelect?: (files: File) => void;
  filesOptions?: OptionsType<OptionType>;
  placeholder?: string;
  documentName?: string;
  filePath?: string;
  update?: boolean;
  prevDoc?: number[];
  setNoScroll?: React.SetStateAction<any>;
  disableAdd?: boolean;
}
interface InitialValue {
  documentSaveName?: string;
}

const DocumentUploader: FC<DocumentUploaderProps> = ({
  index,
  name,
  documentId,
  filesInfo,
  onSelect,
  filesOptions,
  placeholder,
  onUpload,
  onDelete,
  deleteDoc,
  documentName,
  filePath,
  update,
  prevDoc,
  setNoScroll,
  disableAdd,
}) => {
  const [documentSaveName, setDocumentSaveName] = useState<string>('');
  const [files, setFiles] = useState<File | undefined>(filesInfo);
  const [uploadSuccess, setUploadSuccess] = useState<boolean>();
  const classNames = ['custom-file-upload-input'];
  const initialData: InitialValue = {
    documentSaveName,
  };

  const handleFileChange = (event: any) => {
    event.preventDefault();
    const fileSelected = event.target.files[0];
    setFiles(fileSelected);
    if (onSelect) {
      onSelect(fileSelected);
    }
  };

  const handleFileUpload = async (values: InitialValue) => {
    if (files) {
      if (files.size > 10485760) {
        alert('10MB');
        setUploadSuccess(false);
      } else {
        await api.uploadFile(documentSaveName, files).then((res: any) => {
          if (res.status === 200 && res.data != null) {
            if (onUpload) {
              onUpload(res.data.document.documentId, res.data.document.filePath, res.data.document.documentSaveName);
            }
          }
        });
        setNoScroll(true);
        setDocumentSaveName('');
      }
    }
  };

  /**
   * 
   * @param event
   * @param documentId
   */
  const handleFileDelete = async (event: any, id: number) => {
    if (!update && !prevDoc?.includes(id)) {
      api.deleteFile(id).then((res) => {
        if (res.status === 200) {
          if (onDelete) {
            onDelete();
          }
        }
      });
    } else if (onDelete) {
      onDelete(id);
    }
  };

  const handleDownloadFile = async (event: any, id: number, fileName: string) => {
    api.downloadFile(id, { responseType: 'blob' }).then(async (res) => {
      const data = (res.data as any) as BlobPart;
      const blob = new Blob([data]);
      const url = window.URL.createObjectURL(blob);
      const link = document.createElement('a');
      link.href = url;
      link.setAttribute('download', fileName);
      document.body.appendChild(link);
      link.click();
      link.remove();
      window.URL.revokeObjectURL(url);
    });
  };
  return (
    <>
      <Formik initialValues={initialData} onSubmit={(values) => handleFileUpload(values)}>
        {() => (
          <Form>
            {filePath && documentId ? (
              <>
                <Row className="ml-0 mr-0">
                  <Col className="form-group form-inline col-lg-group pr-0 pl-0">
                    <Link
                      className="col-lg-3 col-sm-5 pr-0 pl-0"
                      to="#"
                      onClick={(event) => {
                        if (documentName) {
                          handleDownloadFile(event, documentId, documentName);
                        }
                      }}
                    >
                      {documentName}
                    </Link>
                    {deleteDoc && (
                      <Link
                        className="fa fa-trash-o col-lg-8 pr-1"
                        to="#"
                        onClick={(event) => {
                          handleFileDelete(event, documentId).then();
                        }}
                      />
                    )}
                  </Col>
                </Row>
              </>
            ) : (
              <>
                <div className="custom-file">
                  <Row className="ml-0 mr-0">
                    <Col className="form-group form-inline col-lg-group">
                      <input
                        type="file"
                        name={`${name}`}
                        className={classNames.join(' ')}
                        disabled={uploadSuccess || !deleteDoc}
                        onChange={(event) => handleFileChange(event)}
                      />
                      <label
                        className="custom-file-upload-label pl-2"
                        htmlFor={`${index}`}
                        data-browse=""
                      >
                        {filesInfo ? filesInfo.name : ''}
                      </label>
                    </Col>
                    <Col className="form-group form-inline col-lg-group w-100 pr-4">
                      {filesOptions === undefined ? (
                        <Input
                          className="w-100"
                          name="documentSaveName"
                          placeholder={placeholder}
                          onValueChange={(value: string | number) => {
                            if (value) {
                              setDocumentSaveName(String(value));
                            }
                          }}
                          // value={documentSaveName}
                        />
                      ) : (
                        <Select className="w-100" name="documentKbn" options={filesOptions} placeholder={placeholder} />
                      )}
                    </Col>
                    <Col className="form-group form-inline col-lg-group pl-0">
                      <Button
                        variant="info"
                        className="text-nowrap"
                        type="submit"
                        disabled={uploadSuccess || !deleteDoc || disableAdd}
                        hidden={uploadSuccess}
                      >
                        
                      </Button>
                    </Col>
                  </Row>
                </div>
              </>
            )}
          </Form>
        )}
      </Formik>
    </>
  );
};

export default DocumentUploader;
