import React, { useEffect, useState } from 'react';
import { useHistory, useParams, useLocation } from 'react-router';
import {
  Row,
  Col,
  Button,
  Card,
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
  CardHeader,
  CardBody,
  CardFooter,
  Alert,
} from 'reactstrap';
import { Dropdown, DropdownButton } from 'react-bootstrap';
import { Form, Formik, FormikProps, FieldArray } from 'formik';
import * as Yup from 'yup';
import Breadcrumb from '../components/common/breadcrumb';
import { Input, CustomSelect, Textarea } from '../components/common/form-helper';
import AppLayout from '../components/common/layout';
import DocumentUploader from '../components/common/document-uploader';
import { ScreenUrl } from '../constant/commonConstant';
import SetClientEmployee from '../components/client-process/set-client-employee';
import api from '../utils/api_client';
import {
  ClientEmployeeAttributes,
  ProjectAttributes,
  ServiceAttributes,
  ServiceUpdateAttributes,
  ServiceDocumentAttributes,
  DocumentAttributes,
} from '../api';
import { formatDate, padId } from '../components/common/formatter/formatter';
import CreateClient from '../components/client-process/create-client';
import ConfirmModal from '../components/common/confirm-modal';

interface InitialValue {
  serviceId: number;
  projectCode: string;
  projectName: string;
  serviceStartDate: string;
  serviceEndDate: string;
  serviceCode: string;
  serviceName: string;
  clientCode: string;
  clientName: string;
  employeeCode: string;
  clientEmployeeCode?: string;
  clientEmployeeName?: string;
  departmentName?: string;
  phoneNumber?: string;
  email?: string;
  description: string;
  documents?: serviceDocument[];
}
const blankDocumentInfo: serviceDocument = {
  documentId: 0,
  documentName: '',
  filePath: '',
  documentSaveName: '',
};

interface serviceDocument extends DocumentAttributes {
  filesInfo?: File;
}

/**
 * 
 */
const initialValues: InitialValue = {
  serviceId: 0,
  projectCode: '',
  projectName: '',
  serviceStartDate: '',
  serviceEndDate: '',
  serviceCode: '',
  serviceName: '',
  clientCode: '',
  clientName: '',
  employeeCode: '',
  clientEmployeeCode: '',
  clientEmployeeName: '',
  departmentName: '',
  phoneNumber: '',
  email: '',
  documents: [blankDocumentInfo],
  description: '',
};

const SchemaValidation = Yup.object().shape({
  projectCode: Yup.string().nullable().required().max(8),
  serviceCode: Yup.string().nullable().required().max(12),
  serviceName: Yup.string().nullable().required(),
  clientCode: Yup.string().nullable().required().max(12),
  serviceStartDate: Yup.string().nullable().required(),
  serviceEndDate: Yup.string().nullable().required(),
  employeeCode: Yup.string().nullable().required(),
});

export const Service: React.FC = () => {
  const [modalProject, setModalProject] = useState(false);
  const [modalClient, setModalClient] = useState(false);
  const [modalConfirm, setModalConfirm] = useState(false);
  const [confirm, setConfirm] = useState(false);
  const [projectOptions, setProjectOptions] = useState<{ value: string; label: string }[]>([]);
  const [clientOptions, setClientOptions] = useState<{ value: string; label: string }[]>([]);
  const [employeesOptions, setEmployeesOptions] = useState<{ value: string; label: string }[]>([]);
  const [clientEmployeeData, setClientEmployeeData] = useState<ClientEmployeeAttributes[]>([]);
  const [prevDocuments, setPrevDocuments] = useState<serviceDocument[]>([blankDocumentInfo]);
  const [savedAlert, setSavedAlert] = useState<{ value: boolean; message: string }>({ value: false, message: '' });
  const [copiedAlert, setCopiedAlert] = useState<{ value: boolean; message: string }>({ value: false, message: '' });
  const [copyFlag, setCopyFlag] = useState(false);
  const [preDocId, setPreDocId] = useState<number[]>([]);
  const [delDocId, setDelDocID] = useState<number[]>([]);
  let createdServiceId = 0;

  const [initialData, setInitialData] = useState<InitialValue>(initialValues);
  // eslint-disable-next-line @typescript-eslint/ban-ts-comment
  // @ts-ignore
  const { id } = useParams();
  const location = useLocation();
  const getPartOfName = (startDate: string, endDate: string): string => {
    if (!startDate && !endDate) {
      return '';
    }
    const jsStartDate = new Date(startDate);
    const jsEndDate = new Date(endDate);

    if (startDate && endDate && jsStartDate.getMonth() !== jsEndDate.getMonth()) {
      return `(${String(jsStartDate.getMonth() + 1)}-${String(jsEndDate.getMonth() + 1)})`;
    }
    if (startDate) {
      return `(${String(jsStartDate.getMonth() + 1)})`;
    }
    return `(${String(jsEndDate.getMonth() + 1)})`;
  };

  const fetchData = async () => {
    await api.clientEmployeeGetByClientCode().then((res) => setClientEmployeeData(res.data));
    await api.projectGetAll().then((res) => {
      const projectList = res.data.map((project: { projectCode: string; projectName: string }) => ({
        value: project.projectCode,
        label: `${project.projectCode} ${project.projectName}`,
      }));
      setProjectOptions(projectList);
    });

    await api.clientGetAll().then((res) => {
      const clientList = res.data.map((client) => ({
        value: client.clientCode,
        label: `${client.clientCode} ${client.clientName}`,
      }));
      setClientOptions(clientList);
    });

    await api.employeeGetSales().then((res) => {
      const employeeList = res.data.map((employee: { employeeCode: string; employeeName: string }) => ({
        value: employee.employeeCode,
        label: employee.employeeName,
      }));
      setEmployeesOptions(employeeList);
    });
    if (id) {
      const documentRes = await api.getServiceDocuments(id);
      const documentData = [...documentRes.data, blankDocumentInfo];
      setPrevDocuments(documentData);
      const prevArrDocId: number[] = documentData
        ? documentData.map((doc: serviceDocument) => doc.documentId).filter(Boolean)
        : [];
      setPreDocId(prevArrDocId);
      await api.getServiceById(id).then((res) => {
        const data: InitialValue = {
          ...initialData,
          ...res.data,
          documents: documentData,
        };
        setInitialData({
          ...data,
          serviceStartDate: formatDate(data.serviceStartDate, 'yyyy-MM-dd'),
          serviceEndDate: formatDate(data.serviceEndDate, 'yyyy-MM-dd'),
        });
      });
    } else if (typeof location.state === 'object') {
      setCopyFlag(true);
      setCopiedAlert({ value: true, message: '' });
      setTimeout(() => {
        setCopiedAlert({ value: false, message: '' });
      }, 3000);
      const oldServiceId = (location.state as { serviceId: string }).serviceId;
      api.getServiceDocuments(oldServiceId).then((docs) => {
        setPrevDocuments([...docs.data, blankDocumentInfo]);
      });
    }
  };
  useEffect(() => {
    fetchData().then();
  }, [id]);

  const toggleProject = () => {
    setModalProject(!modalProject);
  };
  const toggleClient = () => {
    setModalClient(!modalClient);
  };

  const handleCreateProject = (formikProps: FormikProps<InitialValue>) => {
    const value = formikProps.values;
    const submitValue: ProjectAttributes = {
      projectCode: value.projectCode,
      projectName: value.projectName,
    };
    api
      .projectPost(submitValue)
      .then(() => {
        projectOptions.push({
          value: value.projectCode,
          label: `${value.projectCode} ${value.projectName}`,
        });
        formikProps.setFieldValue('serviceCode', `${value.projectCode}1`);
        formikProps.setFieldValue(
          'serviceName',
          `${
            (formikProps.values.serviceStartDate !== '' && formikProps.values.serviceStartDate !== null) ||
            (formikProps.values.serviceEndDate !== '' && formikProps.values.serviceEndDate !== null)
              ? projectOptions.filter((p) => p.value === value.projectCode)[0].label.slice(9)
              : ''
          }${getPartOfName(formikProps.values.serviceStartDate, formikProps.values.serviceEndDate)}`
        );
        toggleProject();
      })
      .catch(() => toggleProject());
  };

  const referClientEmployee = async (value: string) => {
    await api.clientEmployeeGetByClientCode(value).then((res) => setClientEmployeeData(res.data));
    setModalConfirm(!modalConfirm);
  };

  const history = useHistory();
  const handleSubmit = async (values: InitialValue) => {
    const serviceValue: ServiceAttributes | ServiceUpdateAttributes = {
      serviceCode: values.serviceCode,
      serviceName: values.serviceName,
      projectCode: values.projectCode,
      serviceStartDate: values.serviceStartDate,
      serviceEndDate: values.serviceEndDate,
      clientCode: values.clientCode,
      clientEmployeeCode: values.clientEmployeeCode !== '' ? values.clientEmployeeCode : undefined,
      employeeCode: values.employeeCode,
      description: values.description !== '' ? values.description : undefined,
    };

    const prevArrDocId: number[] = prevDocuments
      ? prevDocuments.map((doc: serviceDocument) => doc.documentId).filter(Boolean)
      : [];
    const arrDocId: number[] = values.documents
      ? values.documents.map((doc: serviceDocument) => doc.documentId).filter(Boolean)
      : [];
    const createDocId: number[] = [];
    arrDocId.forEach((val) => {
      if (prevArrDocId.indexOf(val) <= -1) createDocId.push(val);
    });
    const serviceDocCreateValue: ServiceDocumentAttributes = {
      serviceId: values.serviceId,
      documents: createDocId,
    };

    if (id) {
      await api.serviceUpdate(id, serviceValue).then(() => {
        createdServiceId = id;
        window.scroll(0, 0);
        setSavedAlert({ value: true, message: '' });
        setTimeout(() => {
          setSavedAlert({ value: false, message: '' });
        }, 3000);
      });
    } else {
      await api.serviceCreate(serviceValue).then((service) => {
        createdServiceId = (service.data as any).serviceId;
        window.scroll(0, 0);
        setSavedAlert({ value: true, message: '' });
        setTimeout(() => {
          setSavedAlert({ value: false, message: '' });
        }, 3000);
      });
    }
    delDocId.forEach((delId) => {
      api.deleteServiceDocument(delId).then(() => {
        api.deleteFile(delId);
      });
    });
    if (createDocId.length > 0)
      await api.createServiceDocument({ ...serviceDocCreateValue, serviceId: createdServiceId });
  };

  const handleService = async (
    value: string | number | string[] | number[] | null,
    formikProps: FormikProps<InitialValue>
  ) => {
    await api.getServiceCode(String(value)).then((res) => {
      if (value !== null && value !== '') {
        formikProps.setFieldValue(
          'serviceCode',
          res.data.length > 0
            ? padId(
                Math.max.apply(
                  '',
                  res.data.map((s) => Number(s.serviceCode))
                ) + 1,
                String(value).length + 1
              )
            : `${String(value)}1`
        );
        formikProps.setFieldValue(
          'serviceName',
          `${
            (formikProps.values.serviceStartDate !== '' && formikProps.values.serviceStartDate !== null) ||
            (formikProps.values.serviceEndDate !== '' && formikProps.values.serviceEndDate !== null)
              ? projectOptions.filter((p) => p.value === value)[0].label.slice(9)
              : ''
          }${getPartOfName(formikProps.values.serviceStartDate, formikProps.values.serviceEndDate)}`
        );
      } else {
        formikProps.setFieldValue('serviceCode', '');
        formikProps.setFieldValue('serviceName', '');
      }
    });
  };

  const callDeleteService = (act: boolean, redirect: string) => {
    if (act) {
      api.deleteService(id).then(() => {
        history.push(redirect, true);
      });
    }
  };

  return (
    <AppLayout>
      <Formik
        initialValues={initialData}
        validationSchema={SchemaValidation}
        onSubmit={(values) => handleSubmit(values)}
        enableReinitialize
      >
        {(formikProps: FormikProps<InitialValue>) => (
          <Form className="form theme-form">
            <Breadcrumb
              parent=""
              title=""
              bookmarkComponent={
                id && (
                  <div className="bookmark pull-right mr-4">
                    <Row>
                      <DropdownButton className="mr-1" title="" variant="info">
                        <Dropdown.Item
                          onClick={() => {
                            history.push({
                              pathname: ScreenUrl.ESTIMATE_CREATE,
                              state: formikProps.values.serviceId,
                            });
                          }}
                        >
                          
                        </Dropdown.Item>
                        <Dropdown.Item
                          onClick={() => {
                            history.push({
                              pathname: `${ScreenUrl.ORDER_CREATE}`,
                              state: { id: formikProps.values.serviceId, from: 's0011' },
                            });
                          }}
                        >
                          
                        </Dropdown.Item>
                        <Dropdown.Item
                          onClick={() => {
                            history.push({
                              pathname: `${ScreenUrl.INVOICE_CREATE}`,
                              state: { id: formikProps.values.serviceId, from: 's0011' },
                            });
                          }}
                        >
                          
                        </Dropdown.Item>
                      </DropdownButton>
                      <Button
                        variant="info"
                        className="mr-1"
                        type="submit"
                        onClick={() => {
                          history.push({
                            pathname: ScreenUrl.SERVICE_CREATE,
                            state: formikProps.values,
                          });
                        }}
                      >
                        <i className="icofont icofont-copy-alt" />
                      </Button>
                      <Button
                        variant="info"
                        className="mr-1"
                        onClick={() => {
                          setConfirm(!confirm);
                        }}
                      >
                        <i className="icofont icofont-trash" />
                      </Button>
                    </Row>
                  </div>
                )
              }
            />
            <div className="container-fluid">
              <div className="row">
                <div className="col-sm-12">
                  <Card>
                    <CardHeader>
                      <h3></h3>
                      <Alert isOpen={savedAlert.value}>{savedAlert.message}</Alert>
                      <Alert isOpen={copiedAlert.value}>{copiedAlert.message}</Alert>
                      {confirm && (
                        <ConfirmModal
                          isOpen={confirm}
                          modalProps={{
                            modalTitle: '',
                            message: '?',
                            yesTitle: '',
                            noTitle: '',
                            functionOnYes: () => callDeleteService(true, `${ScreenUrl.ORDER_MANAGEMENT}`),
                          }}
                        />
                      )}
                    </CardHeader>
                    <CardBody>
                      <Card style={{ boxShadow: 'none' }}>
                        <CardBody>
                          <Modal isOpen={modalProject} toggle={toggleProject}>
                            <ModalHeader toggle={toggleProject}></ModalHeader>
                            <ModalBody>
                              <form>
                                <div className="form-group">
                                  <label className="col-form-label required"></label>
                                  <Input className="form-control" type="text" name="projectCode" />
                                </div>
                                <div className="form-group">
                                  <label className="col-form-label required"></label>
                                  <Input className="form-control" type="text" name="projectName" />
                                </div>
                              </form>
                            </ModalBody>
                            <ModalFooter>
                              <Button
                                variant="info"
                                type="submit"
                                onClick={() => {
                                  handleCreateProject(formikProps);
                                }}
                              >
                                
                              </Button>
                              <Button color="light" onClick={toggleProject}>
                                
                              </Button>
                            </ModalFooter>
                          </Modal>
                          {modalClient && (
                            <CreateClient
                              formikProps={formikProps}
                              initClientCode={formikProps.values.clientCode}
                              clientOptions={clientOptions}
                              isOpen={modalClient}
                            />
                          )}
                          {modalConfirm && (
                            <SetClientEmployee
                              formikProps={formikProps}
                              dataProps={clientEmployeeData}
                              isOpen={modalConfirm}
                            />
                          )}
                          <div className="form-group">
                            <Row>
                              <Col className="form-group form-inline col-lg-group pr-0 pl-0">
                                <label
                                  className="col-lg-3 col-sm-5 pr-0 pl-0 required"
                                  style={{ justifyContent: 'left' }}
                                >
                                  
                                </label>
                                <div className="col-lg-8 pr-1">
                                  {id ? (
                                    `${formikProps.values.projectCode} ${formikProps.values.projectName}`
                                  ) : (
                                    <CustomSelect
                                      name="projectCode"
                                      options={projectOptions}
                                      onChange={(value) => {
                                        handleService(value, formikProps).then();
                                      }}
                                      isClearable
                                    />
                                  )}
                                </div>
                                {!id && (
                                  <i
                                    className="pointer icofont icofont-ui-add"
                                    onClick={() => {
                                      formikProps.setFieldValue(
                                        'projectCode',
                                        padId(
                                          projectOptions.slice(-1)[0]?.value
                                            ? Number(projectOptions.slice(-1)[0].value) + 1
                                            : 1
                                        )
                                      );
                                      formikProps.setFieldValue('projectName', '');
                                      toggleProject();
                                    }}
                                  />
                                )}
                              </Col>
                              <Col className="form-group form-inline col-lg-group pr-0 pl-0">
                                <label
                                  className="col-lg-3 col-sm-5 pr-0 pl-0 required"
                                  style={{ justifyContent: 'left' }}
                                >
                                  
                                </label>
                                <div className="col-lg-4 col-sm-7 ">
                                  <Input
                                    className="w-100"
                                    type="date"
                                    name="serviceStartDate"
                                    onValueChange={(value) => {
                                      if (
                                        (value || formikProps.values.serviceEndDate) &&
                                        formikProps.values.projectCode
                                      ) {
                                        formikProps.setFieldValue(
                                          'serviceName',
                                          `${projectOptions
                                            .filter((p) => p.value === formikProps.values.projectCode)[0]
                                            .label.slice(9)}${getPartOfName(
                                            String(value),
                                            formikProps.values.serviceEndDate ? formikProps.values.serviceEndDate : ''
                                          )}`
                                        );
                                      } else {
                                        formikProps.setFieldValue('serviceName', '');
                                      }
                                    }}
                                  />
                                </div>
                                <span>~</span>
                                <div className="col-lg-4 col-sm-7 w-100">
                                  <Input
                                    className="w-100"
                                    type="date"
                                    name="serviceEndDate"
                                    onValueChange={(value) => {
                                      if (
                                        (value || formikProps.values.serviceStartDate) &&
                                        formikProps.values.projectCode
                                      ) {
                                        formikProps.setFieldValue(
                                          'serviceName',
                                          `${projectOptions
                                            .filter((p) => p.value === formikProps.values.projectCode)[0]
                                            .label.slice(9)}${getPartOfName(
                                            formikProps.values.serviceStartDate
                                              ? formikProps.values.serviceStartDate
                                              : '',
                                            String(value)
                                          )}`
                                        );
                                      } else {
                                        formikProps.setFieldValue('serviceName', '');
                                      }
                                    }}
                                  />
                                </div>
                              </Col>
                            </Row>
                            <Row>
                              <Col className="form-group form-inline col-lg-group pr-0 pl-0">
                                <label
                                  className="col-lg-3 col-sm-5 pr-0 pl-0 required"
                                  style={{ justifyContent: 'left' }}
                                >
                                  
                                </label>
                                <div className="col-lg-8 pr-1">
                                  <Input
                                    className="form-control"
                                    type="text"
                                    name="serviceCode"
                                    style={{ width: '100%' }}
                                  />
                                </div>
                              </Col>
                              <Col className="form-group form-inline col-lg-group pr-0 pl-0">
                                <label
                                  className="col-lg-3 col-sm-5 pr-0 pl-0 required"
                                  style={{ justifyContent: 'left' }}
                                >
                                  
                                </label>
                                <div className="col-lg-8 pr-1">
                                  <Input
                                    className="form-control"
                                    type="text"
                                    name="serviceName"
                                    style={{ width: '100%' }}
                                  />
                                </div>
                              </Col>
                            </Row>
                            <Row>
                              <Col className="form-group form-inline col-lg-group pr-0 pl-0">
                                <label
                                  className="col-lg-3 col-sm-5 pr-0 pl-0 required"
                                  style={{ justifyContent: 'left' }}
                                >
                                  
                                </label>
                                <div className="col-lg-8 pr-1">
                                  <CustomSelect name="clientCode" options={clientOptions} isClearable />
                                </div>
                                <i
                                  className="pointer icofont icofont-ui-add"
                                  onClick={() => {
                                    formikProps.setFieldValue(
                                      'clientCode',
                                      padId(
                                        clientOptions.slice(-1)[0]?.value
                                          ? Number(clientOptions.slice(-1)[0].value) + 1
                                          : 1,
                                        12
                                      )
                                    );
                                    toggleClient();
                                  }}
                                />
                              </Col>
                              <Col className="form-group form-inline col-lg-group pr-0 pl-0" />
                            </Row>
                          </div>
                        </CardBody>
                      </Card>
                      <Card style={{ boxShadow: 'none' }}>
                        <CardHeader
                          className="card-header"
                          style={{
                            padding: '7px',
                            backgroundColor: '#ddd',
                            marginBottom: '0px',
                            borderTopLeftRadius: 0,
                            borderTopRightRadius: 0,
                          }}
                        >
                          <h6 style={{ marginBottom: '0px' }}></h6>
                        </CardHeader>
                        <CardBody style={{ padding: '9px 30px' }}>
                          <div className="form-group">
                            <Row>
                              <Col className="form-group form-inline col-lg-group pr-0 pl-0">
                                <label
                                  className="col-lg-3 col-sm-5 pr-0 pl-0 required"
                                  style={{ justifyContent: 'left' }}
                                >
                                  
                                </label>
                                <div className="col-lg-8 pr-1">
                                  <CustomSelect name="employeeCode" options={employeesOptions} />
                                </div>
                              </Col>
                              <Col className="form-group form-inline col-lg-group pr-0 pl-0" />
                            </Row>
                            <Row>
                              <Col className="form-group form-inline col-lg-group pr-0 pl-0">
                                <Button
                                  type="button"
                                  onClick={() => referClientEmployee(formikProps.values.clientCode)}
                                  disabled={!formikProps.values.clientCode}
                                >
                                  
                                </Button>
                              </Col>
                              <Col className="form-group form-inline col-lg-group pr-0 pl-0" />
                            </Row>
                            <Row>
                              <Col className="form-group form-inline col-lg-group pr-0 pl-0">
                                <label className="col-lg-3 col-sm-5 pr-0 pl-0" style={{ justifyContent: 'left' }}>
                                  
                                </label>
                                <div className="col-lg-8 pr-1">
                                  <Input
                                    className="form-control"
                                    type="text"
                                    name="clientEmployeeName"
                                    style={{ width: '100%' }}
                                    disabled
                                  />
                                </div>
                              </Col>
                              <Col className="form-group form-inline col-lg-group pr-0 pl-0">
                                <label className="col-lg-3 col-sm-5 pr-0 pl-0" style={{ justifyContent: 'left' }}>
                                  
                                </label>
                                <div className="col-lg-8 pr-1">
                                  <Input
                                    className="form-control"
                                    type="text"
                                    name="departmentName"
                                    style={{ width: '100%' }}
                                    disabled
                                  />
                                </div>
                              </Col>
                            </Row>
                            <Row>
                              <Col className="form-group form-inline col-lg-group pr-0 pl-0">
                                <label className="col-lg-3 col-sm-5 pr-0 pl-0" style={{ justifyContent: 'left' }}>
                                  
                                  <br />
                                  
                                </label>
                                <div className="col-lg-8 pr-1">
                                  <Input
                                    className="form-control"
                                    type="text"
                                    name="phoneNumber"
                                    style={{ width: '100%' }}
                                    disabled
                                  />
                                </div>
                              </Col>
                              <Col className="form-group form-inline col-lg-group pr-0 pl-0">
                                <label className="col-lg-3 col-sm-5 pr-0 pl-0" style={{ justifyContent: 'left' }}>
                                  
                                  <br />
                                  
                                </label>
                                <div className="col-lg-8 pr-1">
                                  <Input
                                    className="form-control"
                                    type="text"
                                    name="email"
                                    style={{ width: '100%' }}
                                    disabled
                                  />
                                </div>
                              </Col>
                            </Row>
                          </div>
                        </CardBody>
                      </Card>
                      <Card className="b-r-0" style={{ boxShadow: 'none' }}>
                        <CardHeader
                          className="card-header"
                          style={{
                            padding: '7px',
                            backgroundColor: '#ddd',
                            borderTopLeftRadius: 0,
                            borderTopRightRadius: 0,
                          }}
                        >
                          <h6 style={{ marginBottom: '0px' }}></h6>
                        </CardHeader>
                        <CardBody>
                          <div className="form-group">
                            <FieldArray name="documents">
                              {(arrayHelper) => (
                                <>
                                  {formikProps.values?.documents
                                    ? formikProps.values.documents?.map((document, index) => (
                                        <Row key={`serviceDocument-${index + 1}`}>
                                          <Col className="form-group ">
                                            <DocumentUploader
                                              name={`documents[${index}].documentId`}
                                              documentName={
                                                document.documentSaveName
                                                  ? document.documentSaveName
                                                  : document.documentName
                                              }
                                              documentId={document.documentId}
                                              filePath={document.filePath}
                                              filesInfo={document.filesInfo}
                                              deleteDoc
                                              onSelect={(files: File) => {
                                                arrayHelper.replace(index, {
                                                  ...document,
                                                  filesInfo: files,
                                                  documentName: files.name,
                                                });
                                              }}
                                              onUpload={(
                                                resDocId: number,
                                                resFilePath: string,
                                                resDocumentSaveName: string
                                              ) => {
                                                arrayHelper.replace(index, {
                                                  ...document,
                                                  documentId: resDocId,
                                                  filePath: resFilePath,
                                                  documentName: resDocumentSaveName || document.documentName,
                                                });
                                                arrayHelper.insert(index + 1, blankDocumentInfo);
                                              }}
                                              onDelete={(delId) => {
                                                arrayHelper.remove(index);
                                                if (delId) {
                                                  delDocId.push(delId);
                                                }
                                              }}
                                              update={!!id}
                                              prevDoc={preDocId}
                                            />
                                          </Col>
                                        </Row>
                                      ))
                                    : ''}
                                </>
                              )}
                            </FieldArray>
                            <Row>
                              <Col className="form-group form-inline col-lg-group col-lg-8 pr-0 pl-0">
                                <label
                                  className="col-lg-1 col-sm-5 pr-0 pl-0"
                                  style={{ justifyContent: 'left', alignSelf: 'start' }}
                                >
                                  
                                </label>
                                <div className="col-lg-11">
                                  <Textarea
                                    className="form-control "
                                    type="text"
                                    name="description"
                                    style={{ width: '100%' }}
                                  />
                                </div>
                              </Col>
                            </Row>
                          </div>
                        </CardBody>
                        <CardFooter />
                      </Card>
                      <div className="bookmark pull-right">
                        <DropdownButton style={{ float: 'right' }} title="" variant="info">
                          <Dropdown.Item
                            onClick={() => {
                              formikProps.submitForm().then(() => {
                                setTimeout(() => {
                                  history.push(`${ScreenUrl.ORDER_MANAGEMENT}`);
                                }, 3000);
                              });
                            }}
                          >
                            
                          </Dropdown.Item>
                          <Dropdown.Item
                            onClick={() => {
                              formikProps.submitForm().then(() => {
                                setTimeout(() => {
                                  history.push({
                                    pathname: ScreenUrl.ESTIMATE_CREATE,
                                    state: Number(createdServiceId),
                                  });
                                }, 3000);
                              });
                            }}
                          >
                            
                          </Dropdown.Item>
                          <Dropdown.Item
                            onClick={() => {
                              formikProps.submitForm().then(() => {
                                setTimeout(() => {
                                  history.push({
                                    pathname: ScreenUrl.ORDER_CREATE,
                                    state: { id: createdServiceId, from: 's0011' },
                                  });
                                }, 3000);
                              });
                            }}
                          >
                            
                          </Dropdown.Item>
                          <Dropdown.Item
                            onClick={() => {
                              formikProps.submitForm().then(() => {
                                setTimeout(() => {
                                  history.push({
                                    pathname: ScreenUrl.INVOICE_CREATE,
                                    state: { id: createdServiceId, from: 's0011' },
                                  });
                                }, 3000);
                              });
                            }}
                          >
                            
                          </Dropdown.Item>
                        </DropdownButton>
                      </div>
                    </CardBody>
                  </Card>
                </div>
              </div>
            </div>
          </Form>
        )}
      </Formik>
    </AppLayout>
  );
};
export default Service;
