import { Button, Card, CardBody, CardFooter, CardHeader, Col, Modal, Row } from 'reactstrap';
import React, { useEffect, useReducer, useState } from 'react';
import { Form, Formik, FormikProps } from 'formik';
import { Input } from '../common/form-helper';
import DataTable from '../common/datatable/data-table';
import { padId } from '../common/formatter/formatter';
import api from '../../utils/api_client';
import { ClientEmployeeAttributes } from '../../api';

interface FormProps {
  formikProps: FormikProps<any>;
  dataProps: ClientEmployeeAttributes[];
  isOpen: boolean;
}

const SetClientEmployee: React.FC<FormProps> = ({ formikProps, dataProps, isOpen }) => {
  const [isOpened, setIsOpened] = useState(false);
  const [loading, setLoading] = useState(false);
  const [createFlg, setCreateFlg] = useState(false);

  const reducer = (state: any, action: { id: string }) => ({ checkedId: action.id });

  const iniData: ClientEmployeeAttributes = {
    clientEmployeeCode: '',
    clientEmployeeName: '',
    clientCode: formikProps.values.clientCode,
    departmentName: '',
    phoneNumber: '',
    email: '',
  };

  const [state, dispatch] = useReducer(reducer, {} as any);

  useEffect(() => {
    setIsOpened(isOpen);
    if (dataProps.length <= 0 && formikProps.values.clientCode) setCreateFlg(true);
  }, [dataProps.length, isOpen]);

  // eslint-disable-next-line @typescript-eslint/ban-ts-comment
  // @ts-ignore
  const CheckBox = ({ id }) => (
    <input
      id={id}
      onClick={() => {
        dispatch({ id });
        const row = dataProps.filter((value) => value.clientEmployeeCode === id);
        formikProps.setFieldValue('clientEmployeeCode', row[0].clientEmployeeCode);
        formikProps.setFieldValue('clientEmployeeName', row[0].clientEmployeeName);
        formikProps.setFieldValue('clientCode', row[0].clientCode);
        formikProps.setFieldValue('departmentName', row[0].departmentName);
        formikProps.setFieldValue('phoneNumber', row[0].phoneNumber);
        formikProps.setFieldValue('email', row[0].email);
      }}
      checked={state.checkedId === id}
      type="checkbox"
    />
  );

  const clientEmployeeColumns = [
    {
      name: '',
      minWidth: '10%',
      selector: '',
      cell: (row: { clientEmployeeCode: string }) => <CheckBox id={row.clientEmployeeCode} />,
    },
    {
      name: '',
      selector: 'clientEmployeeCode',
      sortable: true,
      style: { alignItems: 'baseline', paddingTop: '9px' },
      minWidth: '30%',
      cell: (row: { clientEmployeeCode: string }) => (
        <span>{row.clientEmployeeCode ? padId(row.clientEmployeeCode) : ''}</span>
      ),
    },
    {
      name: '',
      selector: 'clientEmployeeName',
      sortable: true,
      style: { alignItems: 'baseline', paddingTop: '9px' },
      minWidth: '50%',
      cell: (row: { clientEmployeeName: string }) => <span>{row.clientEmployeeName}</span>,
    },
  ];

  const toggleConfirm = () => {
    setIsOpened(!isOpened);
  };

  const handleSave = (value: ClientEmployeeAttributes) => {
    const createValue: ClientEmployeeAttributes = {
      clientEmployeeCode: value.clientEmployeeCode,
      clientEmployeeName: value.clientEmployeeName,
      clientCode: formikProps.values.clientCode,
      departmentName: value.departmentName,
      phoneNumber: value.phoneNumber,
      email: value.email,
    };
    if (createFlg) {
      api.createClientEmployee(createValue).then(() => {
        formikProps.setFieldValue('clientEmployeeCode', createValue.clientEmployeeCode);
        formikProps.setFieldValue('clientEmployeeName', createValue.clientEmployeeName);
        formikProps.setFieldValue('clientCode', createValue.clientCode);
        formikProps.setFieldValue('departmentName', createValue.departmentName);
        formikProps.setFieldValue('phoneNumber', createValue.phoneNumber);
        formikProps.setFieldValue('email', createValue.email);
      });
    }
    setIsOpened(!isOpened);
  };

  return (
    <>
      <Modal isOpen={isOpened} toggle={toggleConfirm} size="lg">
        <Formik initialValues={iniData} onSubmit={(value) => handleSave(value)}>
          {() => (
            <Form>
              <Card>
                <CardHeader>
                  <h3></h3>
                </CardHeader>
                <CardBody>
                  {createFlg ? (
                    <>
                      <div className="form-group">
                        <Row>
                          <Col className="form-group form-inline col-lg-group pr-0 pl-0">
                            <label className="col-lg-4 col-sm-5 pr-0 pl-0" style={{ justifyContent: 'left' }}>
                              
                            </label>
                            <div className="col-lg-8 pr-1">
                              <Input type="text" name="clientEmployeeCode" />
                            </div>
                          </Col>
                          <Col className="form-group form-inline col-lg-group pr-0 pl-0">
                            <label className="col-lg-4 col-sm-5 pr-0 pl-0" style={{ justifyContent: 'left' }}>
                              
                            </label>
                            <div className="col-lg-8 pr-1">
                              <Input type="text" name="clientEmployeeName" />
                            </div>
                          </Col>
                        </Row>
                        <Row>
                          <Col className="form-group form-inline col-lg-group pr-0 pl-0">
                            <label className="col-lg-4 col-sm-5 pr-0 pl-0" style={{ justifyContent: 'left' }}>
                              
                            </label>
                            <div className="col-lg-8 pr-1">
                              <Input type="text" name="departmentName" />
                            </div>
                          </Col>
                          <Col className="form-group form-inline col-lg-group pr-0 pl-0">
                            <label className="col-lg-4 col-sm-5 pr-0 pl-0" style={{ justifyContent: 'left' }}>
                              
                            </label>
                            <div className="col-lg-8 pr-1">
                              <Input type="text" name="phoneNumber" />
                            </div>
                          </Col>
                        </Row>
                        <Row>
                          <Col className="form-group form-inline col-lg-group pr-0 pl-0">
                            <label className="col-lg-4 col-sm-5 pr-0 pl-0" style={{ justifyContent: 'left' }}>
                              
                              <br />
                              
                            </label>
                            <div className="col-lg-8 pr-1">
                              <Input type="text" name="email" />
                            </div>
                          </Col>
                          <Col className="form-group form-inline col-lg-group pr-0 pl-0" />
                        </Row>
                      </div>
                    </>
                  ) : (
                    <>
                      <Row className="form-group">
                        <Col className="align-self-start" />
                        <Col className="align-self-center" />
                        <Button variant="info" type="button" onClick={() => setCreateFlg(true)}>
                          
                        </Button>
                      </Row>
                      <Row className="form-group">
                        <Col className="form-group form-inline col-lg-group pr-0 pl-0">
                          <label className="col-lg-4 col-sm-5 pr-0 pl-0"></label>
                          <div className="col-lg-8 pr-1">
                            <Input className="form-control" type="text" name="clientEmployeeCode" />
                          </div>
                        </Col>
                        <Col className="form-group form-inline col-lg-group pr-0 pl-0">
                          <label className="col-lg-4 col-sm-5 pr-0 pl-0"></label>
                          <div className="col-lg-8 pr-1">
                            <Input className="form-control" type="text" name="clientEmployeeName" />
                          </div>
                        </Col>
                      </Row>
                      <Row>
                        <div className="col align-self-start" />
                        <div>
                          <Button variant="info" type="button">
                             <i className="fa fa-search" />
                          </Button>
                        </div>
                        <div className="col align-self-end" />
                      </Row>
                      <Row className="mt-1">
                        <DataTable
                          columns={clientEmployeeColumns}
                          data={dataProps}
                          noHeader
                          subHeaderAlign="left"
                          loading={loading}
                          pagination={false}
                        />
                      </Row>
                    </>
                  )}
                </CardBody>
                <CardFooter>
                  <Row>
                    <div className="col align-self-start" />
                    <div>
                      <Button variant="info" type="submit">
                        {createFlg ? `  ` : ``}
                      </Button>
                    </div>
                    <div className="col align-self-end" />
                  </Row>
                </CardFooter>
              </Card>
            </Form>
          )}
        </Formik>
      </Modal>
    </>
  );
};
export default SetClientEmployee;
