/**
 * ID
 *
 * @remarks
 * ) 1  "00000001"
 *
 * @param id - ID
 * @param length - 
 * @returns ID
 */
export function padId(id?: string | number | null, length = 8): string {
  if (id == null) {
    return ''; // idnullundefined
  }
  return String(id).padStart(length, '0');
}

/**
 * 
 *
 * @param price - 
 * @param addCurrency - true
 * @return 
 */
export function formatPrice(price?: number | null, addCurrency = true): string {
  if (price == null) {
    return ''; // pricenullundefined
  }
  const ret = price.toLocaleString();
  if (addCurrency) {
    return `\xA5${ret}`;
  }
  return ret;
}

/**
 * : yyyyMMdd
 *
 * @param date - yyyy-MM-ddDate
 * @param format - 
 * @returns 
 */
export function formatDate(date?: Date | string | null, format = 'yyyyMMdd'): string {
  if (!date) {
    return ''; // datefalsy()
  }
  const jsDate = new Date(date);
  return format
    .replace(/yyyy/g, String(jsDate.getFullYear()))
    .replace(/MM/g, String(jsDate.getMonth() + 1).padStart(2, '0'))
    .replace(/dd/g, String(jsDate.getDate()).padStart(2, '0'));
}
