import React from 'react';
import DataTable from '../common/datatable/data-table';
import ExpandableComponents from './invoice-expand';

// eslint-disable-next-line react/prop-types,@typescript-eslint/ban-ts-comment
// @ts-ignore
export default ({ data, columns, loading }) => (
  <>
    {data.subExpandInvoice && (
      <DataTable
        responsive={false}
        noHeader
        columns={columns}
        data={data.subChildren}
        loading={loading}
        pagination={false}
        noTableHead
        expandableRows
        expandableRowsHideExpander
        expandableRowExpanded={(row) => row.subExpandInvoice}
        expandableRowsComponent={<ExpandableComponents data={data.children} columns={columns} loading={loading} />}
      />
    )}
    {data.children && (
      <DataTable
        responsive={false}
        noHeader
        columns={columns}
        data={data.children}
        loading={loading}
        pagination={false}
        noTableHead
        expandableRows
        expandableRowsHideExpander
        expandableRowExpanded={(row) => row.expandInvoice}
        expandableRowsComponent={<ExpandableComponents data={data.children} columns={columns} loading={loading} />}
      />
    )}
  </>
);
